<template>
<!-- <transition name="modal-fade"> -->
    <div class="modal-backdrop" @click="close" v-show="show">
        <div class="modal" @click.stop>
            <div class="modal-header">
                <slot name="header">
                    <h3>语音文件正在后台检索中...</h3>
                    <!-- <button type="button" class="btn-close" @click="close">x</button> -->
                </slot>
            </div>
            <div class="modal-body">
                <slot name="body">&nbsp;&nbsp;您可以：<br />
                              (1) 耐心等待后台检索完毕并自动显示详情(仅当结果数不为0时)；<br />
                              (2) 关闭本消息框，手动查看结果。</slot>
            </div>
            <div class="modal-footer">
                <slot name="footer">
                    <button type="button" class="btn-blue" @click="close">关闭</button>
                </slot>
            </div>
        </div>
    </div>
<!-- </transition> -->
</template>


<script>
export default {
    name: "Modal",
    props: ['show'],
    data() {
        return {};
    },
    methods: {
        close: function() {
            this.$emit("close");
        }
    }
};
</script>


<style scoped>
.modal-backdrop {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.3);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100
}
.modal {
    background-color: #fff;
    box-shadow: 2px 2px 20px 1px;
    overflow-x: auto;
    display: flex;
    flex-direction: column;
}
.modal-header,
.modal-footer {
    padding: 15px;
    display: flex;
}
.modal-header {
    border-bottom: 1px solid #eee;
    color: #6e8de3;
    justify-content: space-between;
}
.modal-footer {
    border-top: 1px solid #eee;
    justify-content: flex-end;
}
.modal-body {
    position: relative;
    padding: 20px 10px;
}
.btn-close {
    border: none 0;
    font-size: 20px;
    padding: 20px;
    cursor: pointer;
    font-weight: bold;
    color: #6e8de3;
    background-color: transparent;
}
.btn-blue {
    color: #fff;
    background-color: #6e8de3;
    border: 1px solid #6e8de3;
    border-radius: 4px;
    padding: 4px 8px;
    cursor: pointer;
}

/* .modal-fade-enter,
.modal-fade-leave-active {
    opacity: 0;
}
.modal-fade-enter-active,
.modal-fade-leave-active {
    transition: opacity .3s ease;
} */

.modal {
    font-size: 16px; /* @汤 的panel要引入iview.css, 但iview.css的body设置了默认的font-size: 12px 导致我的样式走样, 故改回之 */
}
</style>


