import Vue from 'vue'
import Router from 'vue-router' /* 引用vue路由模块，并赋值给变量Router */
import myhead from '@/components/myhead'
import login from '../page/login'

import myApplication from '@/page/user/myApplication/index' /* 0522 将 原myApplication目录下的voiceprint和index.vue 提出来与 原myApplication目录同级，改为下行；目前因用不到，暂时注掉 */
// import voiceprint from '@/page/user/index'
// import voice_query from '@/page/user/voiceprint/query'
// import voice_detail from '@/page/user/voiceprint/detail'
// import voice_charts from '@/page/user/voiceprint/charts'
// import voice_print from '@/page/user/voiceprint/index'
// import voice_text from '@/page/user/voiceprint/importText'

// import voice_manage from '@/page/user/myApplication/voiceprint/management' /* 0522 改成下行，并将 原management.vue 改成 query.vue */
import voice_query from '@/page/user/myApplication/voiceprint/query'
import voice_detail from '@/page/user/myApplication/voiceprint/detail' /* 由于拎出来了目录，这几行同样要注掉；改为上面几行的样子 */
import voice_charts from '@/page/user/myApplication/voiceprint/charts'
import voice_print from '@/page/user/myApplication/voiceprint/index'
// import voice_text from '@/page/user/myApplication/voiceprint/importText' /* 0525 原NLP页面, 注释掉 */
import voice_dayIncrement from '@/page/user/myApplication/voiceprint/dayIncrement'
import speaker_manage from '@/page/user/myApplication/voiceprint/speakerManage' /* 0619 add */

Vue.use(Router) /* 因使用了模块化机制编程，则调用路由 */

export default new Router({
    routes: [ /* 定义路由:将组件映射到路由（i.e.进行路由配置，规定“/”引入到myhead组件） */
		{
			path: '/login',
			name: 'login',
			component: login
		},
		{
      		path: '/',
      		name: 'myhead',
              component: myhead, /* 注册myhead组件 */
              redirect: '/login',
      		children: [
      			{
      				path: 'myApplication',
		    		name: 'myApplication',
		    		component: myApplication,
		      		children: [
		      			{
			      			path: 'voiceprint',
			      			name: 'voice_print',
			      			component: voice_print,
			      			children: [
                                //  { path: 'management', name: 'voice_manage', component: voice_manage }, // 0522 改成下行
                                { path: 'increment', name: 'voice_dayIncrement', component: voice_dayIncrement },
								{ path: 'query', name: 'voice_query', component: voice_query },
                                { path: 'detail', name: 'voice_detail', component: voice_detail },
                                { path: 'speakerManage', name: 'speaker_manage', component: speaker_manage },
					      		{ path: 'charts', name: 'voice_charts', component: voice_charts },
					      		// { path: 'text', name: 'voice_text', component: voice_text },
			      			]
			      		},
		      		]
				},
      	
			]
			
		}
  	]
})



