<template>
  <div class="lineChart" v-if='statisticArr'></div>
</template>

<script>
    import echarts from 'echarts'
    import {getStatistics} from '@/api/index.js'

    export default {
        name: 'lineChart',
        data () {
            return {
                chart: null,
                statisticArr: [],
            }
        },
        props: ['lineChartDatenum'],
        watch:{
            lineChartDatenum(newVal,oldVal) {
                // console.log(newVal);
                // console.log('----->',this.getDateRange());
                /* 每次时间变动, 先清空一遍统计数据 */
                this.statisticArr = [];
                getStatistics(this.lineChartDatenum, 0).then(res=>{
                    // console.warn('~~~~~res.data.msg(说话人)__watch()中~~~~~~:', res.data.Data)
                    for (var i = 0; i < this.lineChartDatenum; i++) {
                        this.statisticArr.push(res.data.msg[i].Count)
                    }
                    this.initChart()
                });
            },
        },
        methods:{
            initChart() {
                this.chart = echarts.init(document.querySelector('.lineChart'))
                this.chart.group = 'brick'; // 联动至brick组
                // console.log('%cthis.chart:','color:aqua', this.chart)

                this.chart.setOption({
                    title: {
                        text: '增量趋势图',
                        top: 10,
                        left: 10,
                        textStyle:{
                            fontSize: 16
                        }
                    },
                    legend: {
                        //'circle', 'rect', 'roundRect', 'triangle', 'diamond', 'pin', 'arrow'
                        icon: 'circle',
                        // orient: 'vertical',
                        // data:['有效语音文件', '新增号码', '新增说话人'],
                        data:['新增说话人'],
                        top: 30
                    },
                    tooltip: {
                        trigger: 'axis'
                    },
                    xAxis: {
                        name: '日期',
                        type: 'category',
                        
                        // data: ['06-09','06-10','06-11','06-12','06-13','06-14','06-15']
                        data: this.getDateRange()
                    },
                    yAxis: {
                        type: 'value',
                        name: '数量（条）',
                        axisLabel: {
                            align: 'left',
                            margin: 60
                        }
                    },
                    gfid: {
                        width: '90%',
                        height: '60%',
                        top: '20%',
                        left: 95
                    },
                    series: [
                        // {
                        //     name: '有效语音文件',
                        //     type: 'line',
                        //     smooth: true,
                        //     lineStyle: {
                        //         normal: {
                        //             color: 'rgba(255,69,0,1)',
                        //             width: 5,
                        //             // shadowColor: 'rgba(255,69,0,0.3)',
                        //             // shadowBlur: 15,
                        //             // shadowOffsetY: 15
                        //         }
                        //     },
                        //     itemStyle: {
                        //         normal: {
                        //             color: 'rgba(255,69,0,1)',
                        //         }
                        //     },
                        //     data: [1000, 1200, 210, 540, 260, 830, 710]
                        // },
                        // {
                        //     name: '新增号码',
                        //     type: 'line',
                        //     smooth: true,
                        //     lineStyle: {
                        //         normal: {
                        //             color: 'rgba(218,165,32,1)',
                        //             width: 5,
                        //             // shadowColor: 'rgba(218,165,32,.3)',
                        //             // shadowBlur: 15,
                        //             // shadowOffsetY: 15
                        //         }
                        //     },
                        //     itemStyle: {
                        //         normal: {
                        //             color: 'rgba(218,165,32,1)',
                        //         }
                        //     },
                        //     data: [100, 120, 2100, 1540, 1260, 630, 1110]
                        // },
                        {
                            name: '新增说话人',
                            type: 'line',
                            smooth: true,
                            lineStyle: {
                                normal: {
                                    color: 'rgba(0,160,233,1)',
                                    width: 5,
                                    // shadowColor: 'rgba(0,160,233,.3)',
                                    // shadowBlur: 15,
                                    // shadowOffsetY: 15
                                }
                            },
                            itemStyle: {
                                normal: {
                                    color: 'rgba(0,160,233,1)',
                                }
                            },
                            // data: [1000, 1200, 1200, 5400, 1600, 1360, 1610]
                            data: this.statisticArr
                            // data: this.getStatisticsArray()
                        }
                    ]
                })
            },
            getDateRange() {
                function getDay(day) {
                    var today = new Date();
                    var targetday_milliseconds = today.getTime() + 1000 * 60 * 60 * 24 * day;
                    today.setTime(targetday_milliseconds);
                    var tMonth = today.getMonth();
                    var tDate = today.getDate();
                    tMonth = doHandleMonth(tMonth + 1);
                    tDate = doHandleMonth(tDate);
                    return tMonth + "-" + tDate;
                }
                function doHandleMonth(month) {
                    var m = month;
                    if (month.toString().length == 1) {
                        m = "0" + month;
                    }
                    return m;
                }

                var dateRangeArr = new Array(this.lineChartDatenum);
                for (var i = 1; i <= this.lineChartDatenum; i++) {
                    dateRangeArr[i - 1] = getDay(i - this.lineChartDatenum);
                }
                return dateRangeArr
            },
            getStatisticsArray() {

                // this.statisticArr = new Array(7)
                
                console.warn('~~outside~~:', this.statisticArr)
                // return statisticArr
            }
        },
        beforeCreate() {
        },
        created() {
            // console.log('%cLine_this:','color:green;font-style:italic;font-weight:700' ,this)
            
            // 0 -- 说话人; this.barChartDatenum -- 时间跨度参数: 1/3/7/30天
            getStatistics(this.lineChartDatenum, 0).then(res=>{
                // console.warn('~~~~~res.data.msg(说话人)__created()初始中~~~~~~:', res.data.msg)
                for (var i = 0; i < this.lineChartDatenum; i++) { // 注意: 折线图中, ECharts的横坐标个数会随时间改变, 故而i的上限也需随之改变
                    this.statisticArr.push(res.data.msg[i].Count)
                    // console.warn('statisticArr[i]:',statisticArr[i])
                    // console.warn('async__this:',this)
                }
                // return statisticArr

                this.initChart()
            });
            // console.log('this.getDateRange()----',this.getDateRange());
            // console.log('this.statisticArr-+-+', this.statisticArr)
            // console.error('this:',this)
        },
        beforeMount() {
        },
        mounted() {
            // console.log('init ## lineChar.vue')
            // this.initChart() // 移至ajax中
        },
        beforeUpdate() {
        },
        updated() {
        },
        activated() {
        },
        deactivated() {
        },
        beforeDestroy() {
        },
        destroyed() {
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  .lineChart{
    width: 100%;
    height: 400px;
    margin-bottom: 20px;
    // box-shadow: 0px 0px 10px #888888;
    border-radius: 5px;
    background-color: #fff !important;
  }
</style>


