// import store from '@/store'

import {BASE_API} from './url.js'

// const url = `http://${BASE_API}`
const url = `${BASE_API}`

const service = axios.create({
//   baseURL: '/' /* [0][发布用] api的base_url; 原为 url , 0607打包时要改成 /  */
  baseURL: url
})


export default service


