/* 此处被提交的是异步数据; (1)Action 提交的是 mutation，而不是直接变更状态。(2)Action 可以包含任意异步操作
        ---- actions用于进行递交异步更改，通过调用mutations实现对数据的更改 ----

 */
import * as types from './mutation-types'

export const ACCOUNT = function ({commit}, data) {
  commit(types.ACCOUNT, data)
}
export const RES_DATA = function ({commit}, data) {
  commit(types.RES_DATA, data)
}
export const AUDIO_PATH = function ({commit}, data) {
  commit(types.AUDIO_PATH, data)
}
export const AUDIO_PATHLIST = function ({commit}, obj) {
  commit(types.AUDIO_PATHLIST, obj)
}
export const RES_DATALIST = function ({commit}, data) {
  commit(types.RES_DATALIST, data)
}
export const LIST_INDEX = function ({commit}, data) {
  commit(types.LIST_INDEX, data)
}
export const TASK_ID = function ({commit}, data) {
  commit(types.TASK_ID, data)
}
export const IMPORTTASK_ID = function ({commit}, data) {
  commit(types.IMPORTTASK_ID, data)
}
export const QL_DATA = function ({commit}, obj) {
  commit(types.QL_DATA, obj)
}
export const AUTH = function ({commit}, obj) {
  commit(types.AUTH, obj)
}
export const PHONE_NUMBERS = function ({commit}, data) {
  commit(types.PHONE_NUMBERS, data)
}
export const SECTION_TREE = function ({commit}, data) {
  commit(types.SECTION_TREE, data)
}
export const SPEAKER_ID = function ({commit}, data) {
  commit(types.SPEAKER_ID, data)
}
export const SPEAKER_FILENAME = function ({commit}, data) {
  commit(types.SPEAKER_FILENAME, data)
}
export const REFRESH = function ({commit}, data) {
  commit(types.REFRESH, data)
}
export const CASE_DATA = function ({commit}, obj) {
  commit(types.CASE_DATA, obj)
}
export const PLATFORM = function ({commit}, obj) {
  commit(types.PLATFORM, obj)
}
export const ORIGIN_VOICE = function ({commit}, data) { // 0620 @汤 说话人管理
    commit(types.ORIGIN_VOICE, data)
}
export const TRAIN_VOICE = function ({commit}, data) {
    commit(types.TRAIN_VOICE, data)
}


