<template>
    <div class="slidePanel open" @blur="test">
        <div class="closeBtn" @click="closeBtn"></div>
        <div class="panelContent">
            <div class="title">相关文件</div>
            <div class="btnGroup">
                <el-button-group>
                <el-button class="lbtn" :type=sample @click="searchTabHandle('sample')" >原始文件</el-button>
                <el-button class="mbtn" :type=cluster @click="searchTabHandle('cluster')" >聚类样本</el-button>
                <el-button class="rbtn" :type=train @click="searchTabHandle('train')" >训练样本</el-button>
                </el-button-group>
            </div>
            <div class="list" v-bind:class="{ 'hidden' : sample==='' }">
                <div class="listTitle">
                    <ul>
                        <li>文件名</li>
                        <!-- <li>标注</li> -->
                        <li>删除</li>
                    </ul>
                </div>
                <div class="listContent">
                    <div v-for="(item, index) in sampleFileData" :key="index">
                        <ul class="parent">
                            <li :title="item.fileName" @click="play(item)">{{item.fileName}}</li>
                            <li><a class='delete' @click.self="handleDelete(item, index, 'sample')"></a></li>
                        </ul>
                    </div>
                </div>
            </div>
            <div class="list" v-bind:class="{ 'hidden' : cluster==='' }">
                <div class="listTitle">
                    <ul>
                        <li>文件名</li>
                        <!-- <li>标注</li> -->
                        <li>删除</li>
                    </ul>
                </div>
                <div class="listContent">
                    <div v-for="(item, index) in clusterFileData" :key="index">
                        <ul class="parent">
                            <li :title="item.fileName" @click="play(item)">{{item.fileName}}</li>
                            <li><a class='delete' @click.self="handleDelete(item, index, 'cluster')"></a></li>
                        </ul>
                    </div>
                </div>
            </div>
            <div class="list" v-bind:class="{ 'hidden' : train==='' }">
                <div class="listTitle">
                    <ul>
                        <li>文件名</li>
                        <!-- <li>标注</li> -->
                        <li>删除</li>
                    </ul>
                </div>
                <div class="listContent">
                    <div v-for="(item, index) in trainFileData" :key="index">
                        <ul class="parent">
                            <li :title="item.fileName" @click="play(item)">{{item.fileName}}</li>
                            <li><a class='delete' @click.self="handleDelete(item, index, 'train')"></a></li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {mapActions,mapGetters} from 'vuex'
import {BASE_API_2} from '@/utils/url.js'
import axios from "axios"
export default {
    name: 'slidePanel',
    data () {
        return {
        display: false,
        pathList:[],
        sampleFileData: [],
        clusterFileData: [],
        trainFileData: [],
        sample: 'primary',
        cluster: '',
        train: ''
        }
    },
    props: {
        pass2panel:'',
        panelData:'',
        myMessage: Boolean,
        buttonList:{}
    },
    computed: {
        getAudioPath(){
        //   console.log('getAudioPath()_Entered @ computed @ slidePanel__detail同级!')
        //   console.warn('this.$store @ computed @ slidePanel__detail同级: ', this.$store)
        //   console.log('this.$store.state.audioPathList @ computed @ slidePanel__detail同级: ', this.$store.state.audioPathList) // 空的 !!!
        return this.$store.state.audioPathList.dateFlag
        // return this.$store.state.audioPathList 面板就显示不了数据
        },
    },
    watch: {
        getAudioPath(){
            console.warn('this.pass2panel_slidePanel:',this.pass2panel);
            this.fileData = []
            this.pathList = this.getaudioPathList()

            //   console.log('%cthis.pathList @ watch @ slidePanel__detail同级:','color:green', this.pathList)

            this.getPathsData(this.pathList.samplePaths,'sampleFileData')
            this.getPathsData(this.pathList.clusterPaths,'clusterFileData')
            this.getPathsData(this.pathList.trainPaths,'trainFileData')
            
            // console.log('%cthis.sampleFileData:','color:orange',this.sampleFileData);
            // console.log('%cthis.clusterFileData:','color:red',this.clusterFileData);
            // console.log('%cthis.trainFileData:','color:blue',this.trainFileData);

            // for(let k in this.pathList.clusterPaths){  // @陈 测试用
            //   let fileName = this.pathList.clusterPaths[k].slice(this.pathList.clusterPaths[k].lastIndexOf('/')+1)
            //   this.clusterFileData.push({
            //     'fileName':fileName,
            //     'path':this.pathList.clusterPaths[k]
            //   })
            // }

            // console.log(this.fileData)
        },
        display(newValue, oldValue) {
            if (newValue === false) {
                // console.log('%cthis.$parent.$children:','color:green;font-style:italic',this.$parent.$children)
                this.$parent.$refs.player.wavesurfer.stop();
                this.$parent.activity = 'off';
                /* 面板关闭后, 重置表格内容区高度 */
                setTimeout(function(){
                    let table_content = document.querySelector('.table_content');
                    document.querySelector('.ag-grid.ag-theme-material').style.height = table_content.offsetHeight + 'px';
                }, 20)
            }
        }
    },
    methods:{
        ...mapActions(['AUDIO_PATHLIST','AUDIO_PATH']),
        ...mapGetters(['getaudioPathList']),
        /* 组件自身: 点击侧滑面板上的按钮来控制显示/隐藏 */
        closeBtn (flag) {
            // var count = 1;
            // alert(count);
            // count++;
            
            // console.warn('closeBtn_this:',this)
            if(flag === true){
                this.display = true
            }else{
                this.display = !this.display
                // console.log("%cslidePanel's this: ",'color:red;font-weight:700',this)
            }
            // document.querySelector('.slidePanel').classList.toggle("close")
            // console.log('this.display')
            // console.log(this.display)
            this.display?this.$emit('slidePanel',true):this.$emit('slidePanel',false)
        },
        /* 别的组件调用本组件: 展开panel或隐藏panel */
        panel_show(){
            this.$emit('slidePanel',true)
        },
        panel_hide() {
            this.$emit('slidePanel',false)
        },
        handleDelete(item, index, tab) {
            console.warn("this_delete:",this)
            this.$parent.$refs.player.loadAudio();
            this.$parent.$refs.player.params.playPausebutton = false;
            
            // https://stackoverflow.com/questions/35459010/vuejs-remove-element-from-lists
            if ( tab == 'train' ) {
                /* ---- vm update ---- */
                        /* 1. train那里删除对应文件 */
                this.pathList.trainPaths.splice(index, 1);
                        /* 2. 遍历检测cluster和sample两处, 有对应文件则删除之 */
                let toBeCompared = item.oid;
                for (let j in this.pathList.clusterPaths) {
                    if (this.pathList.clusterPaths[j].Oid == toBeCompared) {
                        this.pathList.clusterPaths.splice(j, 1);
                        break;
                    }
                }
                for (let k in this.pathList.samplePaths) {
                    if (this.pathList.samplePaths[k].Oid == toBeCompared) {
                        this.pathList.samplePaths.splice(k, 1);
                        break;
                    }
                }
                /* ---- view update ---- */
                this.getPathsData(this.pathList.trainPaths,'trainFileData');
                this.getPathsData(this.pathList.clusterPaths,'clusterFileData');
                this.getPathsData(this.pathList.samplePaths,'sampleFileData');
                        /* 3. 数据库删除, 向后台发出ajax请求 */
                axios({
                    url: `${BASE_API_2}/api/sample/` + toBeCompared,
                    method: "delete"
                })
                .then(res => {
                    // console.log(res.data);
                    if (res.data.code === "200") {
                        this.$message({ type: "success", message: "删除成功!" });
                    }
                });

            } else if ( tab == 'cluster' ) {
                /* ---- vm update ---- */
                        /* 1. cluster那里删除对应文件 */
                this.pathList.clusterPaths.splice(index, 1);
                        /* 2. 遍历检测train和sample两处, 有对应文件则删除之 */
                let toBeCompared = item.oid;
                for (let j in this.pathList.trainPaths) {
                    if (this.pathList.trainPaths[j].Oid == toBeCompared) {
                        this.pathList.trainPaths.splice(j, 1);
                        break;
                    }
                }
                for (let k in this.pathList.samplePaths) {
                    if (this.pathList.samplePaths[k].Oid == toBeCompared) {
                        this.pathList.samplePaths.splice(k, 1);
                        break;
                    }
                }
                /* ---- view update ---- */
                this.getPathsData(this.pathList.clusterPaths,'clusterFileData');
                this.getPathsData(this.pathList.trainPaths,'trainFileData');
                this.getPathsData(this.pathList.samplePaths,'sampleFileData');
                        /* 3. 数据库删除, 向后台发出ajax请求 */
                axios({
                    url: `${BASE_API_2}/api/sample/` + toBeCompared,
                    method: "delete"
                })
                .then(res => {
                    if (res.data.code === "200") {
                        this.$message({ type: "success", message: "删除成功!" });
                    }
                });

            } else { // tab 为 sample 时
                /* ---- vm update ---- */
                        /* 1. sample那里删除对应文件 */
                this.pathList.samplePaths.splice(index, 1);
                        /* 2. 遍历检测cluster和train两处, 有对应文件则删除之 */
                let toBeCompared = item.oid;
                for (let j in this.pathList.clusterPaths) {
                    if (this.pathList.clusterPaths[j].Oid == toBeCompared) {
                        this.pathList.clusterPaths.splice(j, 1);
                        break;
                    }
                }
                for (let k in this.pathList.trainPaths) {
                    if (this.pathList.trainPaths[k].Oid == toBeCompared) {
                        this.pathList.trainPaths.splice(k, 1);
                        break;
                    }
                }
                /* ---- view update ---- */
                this.getPathsData(this.pathList.samplePaths,'sampleFileData');
                this.getPathsData(this.pathList.clusterPaths,'clusterFileData');
                this.getPathsData(this.pathList.trainPaths,'trainFileData');
                        /* 3. 数据库删除, 向后台发出ajax请求 */
                axios({
                    url: `${BASE_API_2}/api/sample/` + toBeCompared,
                    method: "delete"
                })
                .then(res => {
                    if (res.data.code === "200") {
                        this.$message({ type: "success", message: "删除成功!" });
                    }
                });
            }
        },
        showDrop (item) {
            if(!item.show){
                for(let k in this.fileData){
                this.fileData[k].show = false
                }
            }
            
            item.show = !item.show
        },
        /* 根据点击的Tab,获取Tab名;默认先获取到primary */
        searchTabHandle (str) {
            // var str = ''
            // this.searchTab = !this.searchTab
            // str = this.lbtn
            // this.lbtn = this. rbtn
            // this.rbtn = str
            this.sample = ''
            this.cluster = ''
            this.train = ''
            this[str] = 'primary'
            // this.lbtn == ''?this.searchTab=false:this.searchTab=true
        },
        test () {
            console.log('blur')
        },
        play (item) {
            // console.log('%cthis及.$parent in play @ slidePanel__detail同级:','color:green;font-weight:700',this)
            this.$parent.activity = 'on';
            // console.log('%cNow playing: ','color:purple;font-weight:700', item)
            // console.log('---------XXXXXX------:this:',this)
            this.AUDIO_PATH(item.path) // 部分路径
            // console.log(item.fileName)
            // console.log(this.$store.state.audioPath)

            /* 解决点击音频后面板上浮,表格内容区高度不随之更新的问题 */
            setTimeout(function(){
                let table_content = document.querySelector('.table_content');
                document.querySelector('.ag-grid.ag-theme-material').style.height = table_content.offsetHeight + 'px';
            }, 20)
        },
        getPathsData (item,str) {
            this[str] = []; // 每次面板获取数据前清空旧有数据
            for(let k in item){
                let fileName = item[k].SrcPath.slice(item[k].SrcPath.lastIndexOf('/')+1)
                // console.log(this[str])
                this[str].push({
                    'oid':item[k].Oid,
                    'fileName':fileName,
                    'path':item[k].SrcPath
                })
            }
        }
    },
    beforeCreate() {
    },
    created() {
        // alert(this.$route)
        // console.log('%cthis.$route @ created @ slidPanel:','color:blue', this.$route)
        // console.log('%cthis @ created @ slidPanel:','color:blue',this.getaudioPathList())
        // 
        // console.log('%cthis_slidePanel @ created','font-weight:700', this)
    },
    beforeMount() {
    },
    mounted() {
    },
    beforeUpdate() {
    },
    updated() {
    },
    activated() {
        // console.log('panel created')
    },
    deactivated() {
    },
    beforeDestroy() {
    },
    destroyed() {
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
    /* 相关文件hover时高亮 */
    .slidePanel .panelContent .list .listContent ul.parent li:hover {
        background: #e5e5ef
    }
    
    // .el-button:hover, .el-button:active {
    //     color: #6e8de3;
    //     border-color: #6e8de3;
    // } /* 对设置为primary的button,有问题 */
    .el-button--primary {
        background-color: #6e8de3;
        border-color: #6e8de3;
    }
    .hidden{
        display: none; 
    }
    .show{
        display: block !important;
    }
    .unfinished{
        color: #da5136;
    }
    .complete{
        color: #36c844;
    }
    .slidePanel{
        font-size: 12px;
        width: 300px;
        height: 100%;
        background-color: #fff;
        box-shadow: 0px 0px 10px #888888;
        // border: 1px solid #000;
        transition: .8s;
        position: absolute;
        z-index: 9;
        top: 0px;
        right: -300px;
        padding: 20px;
        // &.close{
        //   right: -300px !important;
        // }
    
        .closeBtn{
            position: relative; /* 伪元素箭头 0529 add */
            width: 12px;
            height: 46px;
            background-color: #fff;
            box-shadow: -2px 0px 2px #888888;
            position: absolute;
            top: 50%;
            margin-top: -23px;
            left: -12px;
            border-radius: 8px 0 0 8px;
            // border: 1px solid #000;
        }
        /* 伪元素箭头 0529 add */
        .closeBtn:after {
            border: 6px solid transparent;
            border-left: 6px solid #babacc;
            width: 0;
            height: 0;
            position: absolute;
            content: ' ';
            top: 16px;
            left: 4px;
        }
        .panelContent{
            height: 100%;
            .title{
                margin-bottom: 20px;
                font-size: 16px;
                font-weight: 700;
            }
            .btnGroup{
                margin-bottom: 20px;
            }
            .list{
                height: 80%;
                width: inherit;
                border: 1px solid #e5e5ef;

                .listTitle{
                    background-color: #e7edff;
                    ul{
                        height: 40px;
                        line-height: 40px;
                        display: flex;

                        // flex-direction: column;
                        li{
                            text-align: center;
                            // &:nth-child(1){
                            //   width: 50px;
                            //   font-size: 30px;
                            // }
                            &:nth-child(1){
                                width: 70%;
                            }
                            &:nth-child(2){
                                width: 30%;
                                border-left: 1px solid #e0e0e0;
                                // margin-left: 105px; // 0619 add 调整了一下边距,不至于看起来太矬
                            }
                            // &:nth-child(4){
                            //     padding-top: 11px;
                            //     display: flex;
                            //     .delete{
                            //         min-width: 18px;
                            //         width: 18px;
                            //         height: 18px;
                            //         background: url('../../../../../static/images/删除默认_18x18.png');

                            //         &:hover{
                            //             background: url('../../../../../static/images/删除选中_18x18.png');
                            //         }
                            //     }
                            //     .download{

                            //         margin-left: 10px;
                            //         min-width: 18px;
                            //         width: 18px;
                            //         height: 18px;
                            //         background: url('../../../../../static/images/下载默认_18x18.png');

                            //         &:hover{
                            //             background: url('../../../../../static/images/下载选中_18x18.png');
                            //         }
                            //     }
                            // }   
                        }
                    }
                }
                .listContent{
                    cursor: pointer;
                    overflow: auto;
                    height: calc(100% - 40px);
                    ul.parent{
                        height: 40px;
                        line-height: 40px;
                        display: flex;
                        border-bottom: 1px solid #e5e5ef;
                        // flex-direction: column;
                        li{
                            width: 100%;
                            overflow: hidden;
                            white-space: nowrap;
                            text-indent: 10px;
                            // text-align: center;
                            // &:nth-child(1){
                            //   width: 50px;
                            //   font-size: 30px;
                            //   user-select:none;
                            // }
                            &:nth-child(1){
                                width: 70%;
                                text-overflow:ellipsis;
                            }
                            &:nth-child(2){
                                width: 30%;
                                padding-top: 11px;
                                display: flex;
                                justify-content: center;
                                .delete{
                                    min-width: 18px;
                                    width: 18px;
                                    height: 18px;
                                    background: no-repeat center url('../../../../../static/images/删除默认_18x18.png');

                                    &:hover{
                                        background: no-repeat center url('../../../../../static/images/删除选中_18x18.png');
                                    }
                                }
                                .download{
                                    margin-left: 10px;
                                    min-width: 18px;
                                    width: 18px;
                                    height: 18px;
                                    background: no-repeat center url('../../../../../static/images/下载默认_18x18.png');

                                    &:hover{
                                        background: no-repeat center url('../../../../../static/images/下载选中_18x18.png');
                                    }
                                }   
                            }
                            // &:nth-child(2):hover {
                            //     background-color: #ff5e52
                            // }
                        }
                    }
                    .drop{
                        // text-indent: 20px;
                        padding: 20px;
                        background-color: #e5e5ef;
                        line-height: 20px;
                    }

                }
            }
        }
    }
</style>


