export const pickerOptions = [
 {
   text: '今天',
   onClick(picker) {
     const end = new Date()
     const start = new Date(new Date().toDateString())
     end.setTime(start.getTime())
     picker.$emit('pick', [start, end])
   }
 }, {
   text: '最近一周',
   onClick(picker) {
     const end = new Date(new Date().toDateString())
     const start = new Date()
     start.setTime(end.getTime() - 3600 * 1000 * 24 * 7)
     picker.$emit('pick', [start, end])
   }
 }, {
   text: '最近一个月',
   onClick(picker) {
     const end = new Date(new Date().toDateString())
     const start = new Date()
     start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
     picker.$emit('pick', [start, end])
   }
 }, {
   text: '最近三个月',
   onClick(picker) {
     const end = new Date(new Date().toDateString())
     const start = new Date()
     start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
     picker.$emit('pick', [start, end])
   }
 }]


  //时间格式转换
export function dateFormat () {

  Date.prototype.format = function(fmt) { 
    var o = { 
      "M+" : this.getMonth()+1,                 //月份 
      "d+" : this.getDate(),                    //日 
      "h+" : this.getHours(),                   //小时 
      "m+" : this.getMinutes(),                 //分 
      "s+" : this.getSeconds(),                 //秒 
      "q+" : Math.floor((this.getMonth()+3)/3), //季度 
      "S"  : this.getMilliseconds()             //毫秒 
    }; 
    if(/(y+)/.test(fmt)) {
          fmt=fmt.replace(RegExp.$1, (this.getFullYear()+"").substr(4 - RegExp.$1.length)); 
    }
    for(var k in o) {
      if(new RegExp("("+ k +")").test(fmt)){
           fmt = fmt.replace(RegExp.$1, (RegExp.$1.length==1) ? (o[k]) : (("00"+ o[k]).substr((""+ o[k]).length)));
       }
    }
    return fmt; 
  } 
}
//秒转时分秒
export function formatTime(seconds) {
  return [
    parseInt(seconds / 60 / 60),
    parseInt(seconds / 60 % 60),
    parseInt(seconds % 60)
  ]
    .join(":")
    .replace(/\b(\d)\b/g, "0$1");
}
//去重 label：obj的属性
export function dereplication (obj){
  let hash = {}
  obj = obj.reduce(function(item,next){
    hash[next.label] ? '' : hash[next.label] = true && item.push(next)
    return item
  }, [])
  return obj
}
//提示信息
export function messageBox(res,_this) {
  if(!res.data.success){
    _this.$message.error(res.data.message) 
  }else{
    _this.$message({
      message: res.data.message,
      type: 'success'
    })
  }
}
// evade "TypeError: Converting circular structure to JSON"
export function JSONStringify(object) {
    var cache = [];        
    var str = JSON.stringify(object,
        // custom replacer fxn - gets around "TypeError: Converting circular structure to JSON" 
        function(key, value) {
            if (typeof value === 'object' && value !== null) {
                if (cache.indexOf(value) !== -1) {
                    // Circular reference found, discard key
                    return;
                }
                // Store value in our collection
                cache.push(value);
            }
            return value;
        }, 4);
    cache = null; // enable garbage collection
    return str;
};


