<template>
  <div class="slidePanel open" @blur="test">
  	<div class="closeBtn" @click="closeBtn"></div>
    <div class="panelContent" v-show="display">
      <div class="title">{{User}}相关文件</div>
      <!-- {{this.get_origin_voice()}} -->
      <el-tabs v-model="activeName" @tab-click="handleClick" class="tabs">
        <el-tab-pane name="origin" class="tab-content">
          <el-button round type="primary" style="margin-bottom:10px;" @click="dialogVisible = true">导入</el-button>
          <el-button round slot="label">原始文件</el-button>
          <div class="list">
            <div class="listTitle">
              <ul>
                <li>文件名</li>
                <!-- <li style="width:20%">编辑状态</li> -->
                <li>操作</li>
              </ul>
            </div>
            <div class="listContent">
              <div v-for="(item,index) in this.get_origin_voice()" :key="index">
                <ul class="parent">
                  <li :title="item.SrcPath | pathformat()" @click="samplay(item)">
                      (
                      <span :class="{success: (item &&item.Score >= 90) ? true : '', error: true}">{{(item &&item.Score) ? item.Score.toFixed(0) : 0}}</span>
                      )
                      {{item.SrcPath | pathformat() }}
                  </li>
                  <!-- <li class="status" style="width:20%;">未编辑</li> -->
                  <li>
                      <span class="download" @click="handleDownLoad(index, item)" title='下载'></span>
                      <span class="delete" @click="handleDelete(index, item, 'smaple')" title='删除'></span>
                  </li>
                </ul>
                
              </div>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane name="train">
          <el-button round slot="label">训练样本</el-button>
          <el-button round type="primary" style="margin-bottom:10px;" @click.stop="train">训练</el-button>
          <div class="list">
            <div class="listTitle">
              <ul>
                <li>文件名</li>
                <!-- <li style="width:20%">编辑状态</li> -->
                <li>操作</li>
              </ul>
            </div>
            <div class="listContent">
              <div v-for="(item,index) in this.get_train_voice()" :key="index">
                <ul class="parent">
                  <li :title="item.SrcPath | pathformat()" @click="trainplay(item)">
                      (
                      <span :class="{success: (item &&item.Score >= 90) ? true : '', error: true}">{{(item &&item.Score) ? item.Score.toFixed(0) : 0}}</span>
                      )
                      {{item.SrcPath | pathformat()}}
                  </li>
                  <!-- <li class="status" style="width:20%;">未编辑</li> -->
                  <li>
                      <span class='download' @click.self="handleDownLoad(index, item)" title='下载'></span>
                      <span class='delete' @click.self="handleDelete(index, item, 'train')" title='删除'></span>
                    </li>
                </ul>
              </div>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane name="cluster">
          <el-button round slot="label">聚类样本</el-button>
          <div class="list">
            <div class="listTitle">
              <ul>
                <li>文件名</li>
                <!-- <li style="width:20%">编辑状态</li> -->
                <li>操作</li>
              </ul>
            </div>
            <!-- <div class="listContent">
              <div v-for="(item,index) in this.get_train_voice()" :key="index">
                <ul class="parent">
                  <li :title="item.SrcPath | pathformat()" @click="trainplay(item)">
                      (
                      <span :class="{success: (item &&item.Score >= 90) ? true : '', error: true}">{{(item &&item.Score) ? item.Score.toFixed(0) : 0}}</span>
                      )
                      {{item.SrcPath | pathformat()}}
                  </li>
                  <li>
                      <span class='download' @click.self="handleDownLoad(index, item)"></span>
                      <span class='delete' @click.self="handleDelete(index, item)"></span>
                    </li>
                </ul>
              </div>
            </div> -->
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>



    <!-- 自消失提示弹窗 -->
    <el-dialog
      :modal-append-to-body="false"
      title="提示"
      :visible.sync="dialogVisible1"
      width="30%"
    >
      <span>训练中</span>
    </el-dialog>

    <!-- 弹框 -->
    <el-dialog
      :modal-append-to-body="false"
      title="上传原始语音"
      :visible.sync="dialogVisible"
      width="30%"
      :before-close="handleClose"
    >
      <div slot="title">
        <slot name="header">
          <h3 slot="title" class="dialog_title">
            声纹编号:&nbsp;&nbsp;{{ User }}
          </h3>
        </slot>
      </div>

      <el-form ref="form" :rules="dialog_form_rules" :model="dialog_form" label-width="70px" label-position="left"><!--更改了属性 label-position="top"-->

      <!-- <el-form-item label="声纹编号" prop="User" disabled>
        <el-input v-model="dialog_form.User" placeholder="仅支持半角字母、数字、_"></el-input>
      </el-form-item> -->

      <el-form-item label="格式" prop="Fmt">
        <el-select v-model="dialog_form.Fmt" placeholder="请选择格式" style="width: 100%;">
          <el-option label="pcm" value="pcm"></el-option>
          <el-option label="alaw" value="alaw"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="采样率" prop="Rate">
        <el-select v-model="dialog_form.Rate" placeholder="请选择性别" style="width: 100%;">
          <el-option label="8000" value="8000"></el-option>
          <el-option label="16000" value="16000"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="位" prop="Bits">
        <el-input v-model="dialog_form.Bits" disabled></el-input>
      </el-form-item>

      <el-form-item label="声道" prop="Channels">
        <el-input v-model="dialog_form.Channels" disabled></el-input>
      </el-form-item>


      <el-form-item label="文件" prop="File">
        <div class="files">
          <div class="file">
              <Icon type="plus-round"></Icon>
              <form ref="formreset">
                <input type="file" id="resource" name="resource" ref="resource" multiple @change="getfilelist">
              </form>
          </div>
          
          
        </div>
        <!-- <input type="file" multiple > -->
        <el-input v-model="dialog_form.File"></el-input>
      </el-form-item>
    </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click.stop="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click.stop="dialog_submit">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions,mapGetters } from "vuex";
import * as utils from "@/utils/url";
import pathformat from "../../filters/pathformat";
import { BASE_API, BASE_API_2 } from "@/utils/url.js";
import axios from "axios";
import Bus from './bus.js'
export default {
  name: "slidePanel",
  data() {
    return {
      origin_voice_data: [],
      train_voice_data: [],
      display: false,
      pathList: [],
      lbtn: "",
      mbtn: "",
      rbtn: "primary",
      activeName: "origin",
      dialogVisible: false,
      dialogVisible1: false,
      dialog_form: {
        // User: 1,
        Fmt: "pcm",
        Rate: 8000,
        Bits: 16,
        Channels: 1,
        File: ""
      },
      dialog_form_rules: {
        User: [{ required: this.isedit?true:false, message: "声纹编号不能为空", trigger: "blur" }],
        NickName: [{ required: true, message: "声纹名称不能为空", trigger: "blur" }],
        Lang: [
          { required: true, message: "请选择语种", trigger: "change" }
        ],
        Gender: [
          { required: true, message: "请选择性别", trigger: "change" }
        ],
        Threshold: [
          { required: true, message: "请输入阈值", trigger: "change" }
        ],
        strategy: [{ required: true, message: "请选择活动区域", trigger: "change" }],
        category: [{ required: true, message: "请选择活动区域", trigger: "change" }],
        AppType: [
          {
            required: true,
            message: "请选择类型",
            trigger: "change"
          }
        ],
        Usage: [{ required: true, message: "请选择", trigger: "change" }],
        // desc: [{ required: true, message: "请填写活动形式", trigger: "blur" }]
      },
    };
  },
    props: {
        User: {type: String, default: '提示'},
        fileData: { type: Array, default: () => { return [] } },
        originfileData: { type: Array, default: () => { return [] } },
        trainfileData: { type: Array, default: () => { return [] } }
    },
    filters: {
        pathformat
    },
    methods: {
        ...mapGetters(["get_origin_voice", "get_train_voice"]),
        ...mapActions(['AUDIO_PATH','ORIGIN_VOICE','TRAIN_VOICE']),
        train() {
            // console.log(this.User)
            this.dialogVisible1 = true;
            let formData = new FormData();
            formData.append("User", this.User);
            formData.append("Policy",1);
            axios({
                url: `${BASE_API_2}/api/train`,
                data: formData,
                method: 'post'
            })
            .then((res) => {
                console.log(res.data);
                if (res.data.code === "200") {
                    this.dialogVisible1 = false;
                    this.$message({ type: "success", message: res.data.msg.info || "操作成功!" });
                } else {
                    this.dialogVisible1 = false;
                    this.$message({ type: 'error', message: res.data.msg.info })
                }
                // this.$router.go(0);
                this.closeBtn();
                this.$emit("listenToChildEvent",true);
            })
        },
        getfilelist() {
            let FileList = Array.prototype.slice.call(this.$refs.resource.files);
            let arr = [];
            FileList.map((file, index) => {
                if (index < 9) {
                    arr.push(file.name);
                } else {
                    return;
                }
            });
            this.dialog_form.File = arr.join(", ");
        },
        dialog_submit() {
            // console.log(formData)
            let FileList = Array.prototype.slice.call(this.$refs.resource.files);
            // let arr = [];
            // FileList.map((file, index) => {
            //   if (index < 9) {
            //     arr.push({"SrcPath": file.name, "Score": 0});
            //   } else {
            //     return;
            //     // 20180613_093028_1_test1-A.wav

            //   }
            // });
            // console.log(arr);
            // return;
            // this.ORIGIN_VOICE(arr);
            // this.dialogVisible = false;
            // this.originfileData = arr;
            // return;
            FileList.map((e) => {
                let data = this.dialog_form;
                let formData = new FormData();
                for ( let k in data ) {
                    formData.append(k,data[k])
                }
                formData.append("User",this.User);
                formData.append("Type",0);
                formData.append("Policy",1);
                formData.append("File", e);

                axios({
                    url: `${BASE_API_2}/api/sample`,
                    method: "post",
                    data: formData
                })
                .then((response) => {
                    console.log(response.data);
                    let res = response.data;
                    if ( res.code === "200" ) {
                        this.dialogVisible = false;
                        console.log('this.User__submit: ',`${this.User}`)
                        axios({
                            url: `${BASE_API_2}/api/samples/${this.User}/1`
                        })
                        .then((response) => {
                            // console.log(response.data)
                            this.ORIGIN_VOICE(response.data.msg);
                        })
                    }
                })
            })
            // axios({url: `/api/samples/${this.User}/1`}).then((response) => {
            //   console.log(response.data)
            // })
            // formData.append("File",FileList);
            // let dialog_form_data = Object.assign(formData,{"File": FileList})
        
        },
        handleClose(done) {
            this.$confirm('确认关闭？')
                .then(_ => {
                    done();
                })
                .catch(_ => {});
        },
        closeBtn() {
            this.display = !this.display;
            this.$emit("slidePanel", this.display);
        },
        handleClick(tab, event) {
            // console.log(tab, event);
        },
        showDrop(item) {
            if (!item.show) {
                for (let k in this.fileData) {
                    this.fileData[k].show = false;
                }
            }

            item.show = !item.show;
        },
        searchTabHandle(str) {
            this.lbtn = "";
            this.mbtn = "";
            this.rbtn = "";
            this[str] = "primary";
        },
        test() {
            console.log("blur");
        },
        handleDownLoad(index, item) {
            let url = `${BASE_API_2}` + item.SrcPath;
            var elemIF = document.createElement("iframe");
            elemIF.src = url;    
            elemIF.style.display = "none";     
            document.body.appendChild(elemIF); 
        },
        handleDelete(index, item, name) {
            console.warn('delete__Item:', item);
            this.$parent.$refs.media.wavesurfer.load();
            this.$parent.$refs.media.params.playPause = false;

            let toBeDeleted = item.Oid;
            axios({
                url: `${BASE_API_2}/api/sample/` + toBeDeleted,
                method: "delete"
            })
            .then(res => {
                // console.log(res.data);
                if (res.data.code === "200") {
                    this.$message({ type: "success", message: "删除成功!" });

                    if (name == 'sample') {
                        for (let i in this.get_origin_voice()) {
                            if (this.get_origin_voice()[i].Oid == toBeDeleted) {
                                this.get_origin_voice().splice(i, 1);
                                break;
                            }
                        }
                    } else if (name == 'train') {
                        for (let i in this.get_train_voice()) {
                            if (this.get_train_voice()[i].Oid == toBeDeleted) {
                                this.get_train_voice().splice(i, 1);
                                break;
                            }
                        }
                    }
                }
            });
        },
        samplay(item) {
            // console.log(item);
            this.$emit("showMedia", true);
            Bus.$emit("getTarget",item)
            // let path = `${window.location.origin}${item.SrcPath}`;
            let path = `${BASE_API_2}${item.SrcPath}`;
            console.log(path);
            this.AUDIO_PATH(path);

            // console.warn('this.$parent.$refs.media:',this.$parent.$refs.media)
            // this.$parent.$refs.media.showWavesurfer();
            // this.$parent.$refs.media.wavesurfer.play()
            
        },
        trainplay(item) {
            this.$emit("showMedia", true);
            /* 点击播放的时候获取每一条的路径，通过AUDIO_PATH存入store中 */
            // console.log(`${item.trainpath}`);
            // let path = `${window.location.origin}${item.SrcPath}`;
            let path = `${BASE_API_2}${item.SrcPath}`;
            console.log(path);
            // this.$store.commit("getAudioPath",path);
            // console.log(this.$store.state.audioPath);
            // this.AUDIO_PATH(this.$store.state.audioPath);
            this.AUDIO_PATH(path);
            // console.log(item.fileName)
            // console.log(this.$store.state.audioPath)
        }
    },
    mounted() {
        // console.log(this.get_origin_voice())
        // console.log(this.$store.state.origin_voice);
    },
    watch: {
        get_origin_voice() {
            console.log(this.$store.state.origin_voice)
        }
    },
  // computed: {
  //   get_origin_voice() {
  //     return this.$store.state.origin_voice;
  //   }
  // }
};
</script>

<style type="text/css">
.el-dialog__body {
    padding: 30px;
}
.slidePanel .el-tabs__item {
    padding-right: 0;
}
.slidePanel .el-button.is-round {
    padding: 6px 12px;
}
</style>

<style scoped lang="scss">
    .unfinished {
        color: #da5136;
    }
    .complete {
        color: #36c844;
    }
    .error {
        color: #f17f7f;
    }
    .success {
        color: #4cbf40;
    }
    .slidePanel {
        font-size: 12px;
        height: 100%;
        // background-color: #e5e5ef;
        background-color: #fff;
        box-shadow: 0px 0px 10px #888888;
        position: relative;
        padding: 20px;
        .closeBtn {
            width: 12px;
            height: 46px;
            background-color: #fff;
            box-shadow: -2px 0px 2px #888888;
            position: absolute;
            top: 50%;
            margin-top: -23px;
            left: -12px;
            border-radius: 8px 0 0 8px;
        }
        .closeBtn:after {
            border: 6px solid transparent;
            border-left: 6px solid #babacc;
            width: 0;
            height: 0;
            position: absolute;
            content: ' ';
            top: 16px;
            left: 4px;
        }
        .panelContent {
            width: 300px;
            height: 100%;
            display: flex;
            flex-direction: column;
            .title {
                margin-bottom: 20px;
                font-size: 16px;
                font-weight: 700;
            }
            .tabs {
                height: 100%;
                flex: 1;
                .tab-content {
                    height: 100%;
                }
            }
            .btnGroup {
                margin-bottom: 20px;
            }
            .list {
                height: 80%;
                border: 1px solid #e5e5ef;
            
                .listTitle {
                    background-color: #e7edff;
                    ul {
                        height: 40px;
                        line-height: 40px;
                        display: flex;
                        li {
                            text-align: center;
                            &:nth-child(1){
                                width: 70%;
                            }
                            &:nth-child(2) {
                                width: 30%;
                                border-left: 1px solid #e0e0e0;
                            }
                        }
                    }
                }
                .listContent {
                    overflow: auto;
                    height: calc(100% - 40px);
                    ul.parent {
                        height: 40px;
                        line-height: 40px;
                        display: flex;
                        border-bottom: 1px solid #e5e5ef;
                        // flex-direction: column;
                        li {
                            width: 100%;
                            overflow: hidden;
                            white-space: nowrap;
                            text-indent: 10px;
                            // text-align: center;
                            // &:nth-child(1){
                            //   width: 50px;
                            //   font-size: 30px;
                            //   user-select:none;
                            // }
                            &:nth-child(1) {
                                // width: 70px;
                                width: 70%;
                                text-overflow: ellipsis;
                                &:hover {
                                    background: #e5e5ef;
                                    cursor: pointer;
                                }
                            }
                            &:nth-child(2) {
                                width: 30%;
                                // padding-top: 11px;
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                .delete {
                                    width: 50%;
                                    min-width: 18px;
                                    height: 100%;
                                    background: no-repeat center url('../../../../../static/images/删除默认_18x18.png');

                                    &:hover {
                                        background: no-repeat center url('../../../../../static/images/删除选中_18x18.png') #e5e5ef;
                                        cursor: pointer;
                                    }
                                }
                                .download {
                                    width: 50%;
                                    min-width: 18px;
                                    height: 100%;
                                    background: no-repeat center url('../../../../../static/images/下载默认_18x18.png');

                                    &:hover {
                                        background: no-repeat center url('../../../../../static/images/下载选中_18x18.png') #e5e5ef;
                                        cursor: pointer;
                                    }
                                }
                            }
                        }
                    }
                    .drop {
                        // text-indent: 20px;
                        padding: 20px;
                        background-color: #e5e5ef;
                        line-height: 20px;
                    }
                }
            }
        }
    }
    .files {
        // width: 360px;
        width: 100%;
        height: 100px;
        background-color: #fbfbfb;
        border-radius: 3px;
        border: solid 1px #dddeea;
        display: flex;
        justify-content: center;
        align-items: center;
        .file {
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 12px;
            font-size: 30px;
            overflow: hidden;
            color: #cdcde5;
            width: 100%;
            height: 100%;
            background-color: #f2f2f8;
            border-radius: 3px;
            border: dashed 1px #dddeea;
        }
        .file input {
            position: absolute;
            font-size: 100px;
            right: 0;
            top: 0;
            left: 0;
            bottom: 0;
            opacity: 0;
        }
        .file:hover {
            background: #aadffd;
            border-color: #78c3f3;
            color: #004974;
        }
    }
</style>


