/* 此处被提交的是同步的数据; mutation 必须是同步函数
        ---- mutations用于递交更改，对state对象中的属性数据进行更改 ----
 */
import * as types from './mutation-types'

const mutations = {
  [types.ACCOUNT](state, data) {
    state.account = data
  },
  [types.RES_DATA](state, data) {
    state.resdata = data
  },
  [types.AUDIO_PATH](state, data) {
    state.audioPath = data
  },
  [types.AUDIO_PATHLIST](state, obj) {
    state.audioPathList.samplePaths = obj.SamplePaths
    state.audioPathList.clusterPaths = obj.ClusterPaths
    state.audioPathList.trainPaths = obj.TrainPaths
    state.audioPathList.dateFlag = obj.dateFlag
  },
  [types.RES_DATALIST](state, data) {
    state.resdataList = data
  },
  [types.LIST_INDEX](state, data) {
    state.listIndex = data
  },
  [types.TASK_ID](state, data) {
    state.taskId = data
  },
  [types.IMPORTTASK_ID](state, data) {
    state.importTaskId = data
  },
  [types.QL_DATA](state, obj) {
    state.qlData.caller = obj.caller
    state.qlData.callee = obj.callee
    state.qlData.starttime = obj.starttime
    state.qlData.opname = obj.opname
    state.qlData.returnTo = obj.returnTo
  },
  //auth:{
  //   sys_userManagement: null,   //1  用户管理
  //   search_defraud: null,       //21 诈骗
  //   search_condition: null,     //8  检索
  //   mon_list: null,             //20 实时预警
  //   search_importVoice: null,   //11 导入语音
  //   mycase_man: null,           //3  案件管理
  //   res_keywords: null,         //5  关键词
  //   res_speakers: null,         //6  声纹
  //   res_speechseg: null,        //4  固定音频
  //   aaaaaaa: null,              //12  状态监测
  //   bbbbbbb: null,              //14  搜索队列
  // } 
  [types.AUTH](state, obj) {
    state.auth.sys_userManagement = obj.userManagement
    state.auth.search_defraud = obj.defraud
    state.auth.search_condition = obj.condition
    state.auth.mon_list = obj.monitior
    state.auth.search_importVoice = obj.importVoice
    state.auth.mycase_man = obj.mycase
    state.auth.res_keywords = obj.keywords
    state.auth.res_speakers = obj.speakers
    state.auth.res_speechseg = obj.speechseg
    state.auth.aaaaaaa = obj.aaaaaaa
    state.auth.bbbbbbb = obj.bbbbbbb

  },
  [types.PHONE_NUMBERS](state, data) {
    state.phoneNumbers = data
  },
  [types.SECTION_TREE](state, data) {
    state.sectionTree = data
  },
  [types.SPEAKER_ID](state, data) {
    state.speakerId = data
  },
  [types.SPEAKER_FILENAME](state, data) {
    state.speakerFilename = data
  },
  [types.REFRESH](state, data) {
    state.refresh = data
  },
  [types.CASE_DATA](state, obj) {
    state.caseData.keywords = obj.keywords
    state.caseData.speechsegs = obj.speechsegs
    state.caseData.telrules = obj.telrules
    state.caseData.id = obj.caseid
  },
  [types.PLATFORM](state, obj) {
    state.platform.platform = obj.platform
    state.platform.platformOptions = obj.platformOptions
  },
  [types.ORIGIN_VOICE](state, data) { // 0620 @汤 说话人管理
    state.origin_voice = data
  },
  [types.TRAIN_VOICE](state, data) {
    state.train_voice = data
  },
}

export default mutations


