// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

import store from './store'

import "ag-grid-enterprise/main";

import "ag-grid/dist/styles/ag-grid.css";
import "ag-grid/dist/styles/theme-fresh.css";
import "ag-grid/dist/styles/theme-material.css";
import "ag-grid/dist/styles/ag-theme-material.css";

//css
// import './page/user/assets/less/index.css'; // 0620 @汤  说话人管理 侧滑面板中
// import "./page/user/assets/less/common.scss"

/*  -------引入iview并使用--------  */
import iView from 'iview'
import 'iview/dist/styles/iview.css';
Vue.use(iView)


Vue.config.productionTip = false

Vue.use(ElementUI)

/* eslint-disable no-new */
new Vue({
  store: store, /* 通过在根实例中注册 store 选项，该 store 实例会注入到根组件下的所有子组件中，且子组件能通过 this.$store 访问到 -- vuex */
  el: '#app',          /* 定义作用范围就是index.html里的id为app的范围内 */
  router,              /* 引入路由 */
  components: { App }, /* 注册引入的组件App.vue */
  template: '<App/>'   /* 给Vue实例初始一个App组件作为template,相当于默认组件;
                          将app组件替换掉#app的那个div，同时App也是一个组件，在components里面必须要声明*/
})



