<template>
  <div class="pieChart"></div>
</template>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  .pieChart{
    width: calc(50% - 10px);
    height: 400px;
    // box-shadow: 0px 0px 10px #888888;
    border-radius: 5px;
    background-color: #fff !important;
  }
</style>

<script>
import echarts from 'echarts'
import {getStatistics} from '@/api/index.js'

export default {
    name: 'pieChart',
    data () {
        return {
          chart: null,
          statisticArr: [],
        }
    },
    props: ['pieChartDatenum'],
    watch: {
        pieChartDatenum() {
            getStatistics( this.pieChartDatenum, 2).then(res=>{
                // console.warn('~~~~~res.data.Data(性别)__watch()中~~~~~~:', res.data.Data)
                var tmp = [{value: 0, name: '男性'}, {value: 0, name: '女性'}, {value: 0, name: '未知'}]
                tmp[0].value = res.data.msg[1].Count;
                tmp[1].value = res.data.msg[2].Count;
                tmp[2].value = res.data.msg[0].Count;
                this.statisticArr = tmp;
                this.initChart()
            });
        }
    },
    methods:{
        initChart() {
            this.chart = echarts.init(document.querySelector('.pieChart'))
            this.chart.group = 'brick';
            // console.log('%cthis.chart:','color:red', this.chart)

            this.chart.setOption({
                title: [
                    {
                        text: '性别比例图',
                        top: 10,
                        left: 10,
                        textStyle:{
                            fontSize: 16
                        }
                    },
                    {
                        text:'总数',
                        x:'center',
                        top: '45%',
                        textStyle: {
                            color: '#999',
                            fontWeight: 'normal',
                            fontSize: 14
                        }
                    },
                    {
                        // text:'8465541',
                        text: this.statisticArr[0].value + this.statisticArr[1].value,
                        x:'center',
                        top: '51%',
                        textStyle: {
                            color: "#333",
                            fontWeight: 'normal',
                            fontSize: 26,
                            fontFamily: 'MyNewFont'
                        }
                    }
                ],
                tooltip: {
                    trigger: 'axis',
                    // formatter: '{b}: {c}<br />{a}: {b}'
                },
                legend: {
                    orient: 'vertical',
                    right: '30',
                    bottom: '30',
                    data:['男性','女性','未知']
                },
                color:['#4385ff','#ff7843','#6e7f80'],

                series: [
                    {
                        name: '上海市男女比例',
                        type: 'pie',
                        radius: ['45%', '60%', '75%'],
                        center: ['50%','55%', '60%'],
                        avoidLabelOverlap: false,
                        // data: [
                        //     {value: 65465, name: '女性'},
                        //     {value: 26564, name: '男性'}
                        // ],
                        data: this.statisticArr,
                        // 普通样式
                        itemStyle: {
                            normal: {
                                // shadowColor: 'rgba(255,138,0,0.3)',
                                // shadowBlur: 30,
                                // shadowOffsetY: 0,
                                label:{ 
                                    fontSize: 14,
                                    show: true, 
                                    // 标签的文字
                                    formatter: '{b} : {c} ({d}%)' 
                                }
                            },
                            // labelLine :{show:true}
                        }
                    }
                ]
            })
        }
    },
    beforeCreate() {
    },
    created() {
        // console.log('%cPie_this:','color:red;font-style:italic;font-weight:700' ,this)
        
        // 2 -- 性别; this.barChartDatenum -- 时间跨度参数: 1/3/7/30天
        getStatistics( this.pieChartDatenum, 2 ).then(res=>{
            // console.warn('~~~~~res.data.Data(性别)_created()初始中~~~~~~:', res.data.Data)
            var tmp = [{value: 0, name: '男性'}, {value: 0, name: '女性'}, {value: 0, name: '未知'}]
            tmp[0].value = res.data.msg[1].Count;
            tmp[1].value = res.data.msg[2].Count;
            tmp[2].value = res.data.msg[0].Count;
            // console.warn('tmp(性别):',tmp)

            this.statisticArr = tmp;
            
            this.initChart()
        });
    },
    beforeMount() {
    },
    mounted() {
        // console.log('init ## pieChart.vue')
        // this.initChart()
    },
    beforeUpdate() {
    },
    updated() {
    },
    activated() {
    },
    deactivated() {
    },
    beforeDestroy() {
    },
    destroyed() {
    }
}
</script>
<style lang="scss" scoped>
    @font-face {
        font-family: 'MyNewFont';   /*字体名称*/
        src: url('../../../../../../static/DIN-Bold.otf');       /*字体源文件*/
    }
</style>




