<script>
export default {
    name: 'my-page',
    props: {
        cur: { type: [Number, String], default: 0 },
        line: { type: [Number, String], default: 0 },
        total: { type: [Number, String], default: 0 }
    },
    methods: {
        pagechange (index) {
            this.$emit('pagechange', index)
        },
        sizechange (index) {
            this.$emit('sizechange', index)
        }
    }
}
</script>

<template>
    <el-pagination
        layout="total, sizes, prev, pager, next, jumper"
        v-if="Number(total) > 0"
        @current-change="pagechange"
        @size-change="sizechange"
        :current-page="Number(cur)" :page-size="Number(line)" :total="Number(total)">
    </el-pagination>
</template>

<style scoped lang="less">
</style>



