<template>
  <div class="editor" @click.ctrl="onClick">
    <!-- <div class="title">
      <div class="backto" @click="goBack">
        <i><</i>
        <span>返回</span>
      </div>
      <div class="fileinfo">
        <span>文件名：</span>
        <span>{{$route.params.filePath}}</span>
        <span>播放速度：</span>
        <span>125%</span>
      </div>
    </div> -->
    
    <div class="graph">
      <!-- <i-circle :percent="percent" style="position:absolute;top:50%;left:50%;z-index:999;" v-show="isShowCircle">
        <span class="demo-Circle-inner" style="font-size:24px">{{percent}}%</span>
      </i-circle> -->
      <!-- <div class="musk">
alc
      </div> -->
      <!-- <el-progress type="circle" :percentage="percent" style="width:100%;position:absolute;top:10%;left:50%;" v-show="isShowCircle">
        <span class="demo-Circle-inner" style="font-size:24px">{{percent}}%</span>
      </el-progress> -->
      <div class="wave">
        <div id="waveform" @mousewheel="zoom($event)"></div>
        <div id="waveform-timeline"></div>
      </div>      
      <div class="wave-controll">
        <ul ref="btns">
          <li class="left">
            <!-- id="playPausebutton" :class="{ 'on' : params.playPausebutton }" title="播放/暂停" -->
            <div title="慢放" @click="skipBackwardbutton" class="skip_backward"></div>
            <div :class="{ 'on' : params.playPause }" title="播放/暂停" @click="playPausebutton" class="play_pause"></div>
            <div title="快放" @click="skipForwardbutton" class="skip_forward"></div>
            <div :class="{ 'on' : params.stop }" title="停止" @click="stopbutton" class="stop"></div>
            <div :class="{ 'on' : params.toggleMute }" class="sound" @click="toggleMutebutton"></div>
            <div class="volumn">
              <input id="volButton" type="range" min="0" max="1" v-model="volumn" step="0.01" />
            </div>
            <div title="放大" class="zoom_in" @click="zoomInbutton"></div>
            <div title="缩小" class="zoom_out" @click="zoomOutbutton"></div>
          </li>
          <li class="right">
            <!-- <div></div> -->
            <div title="复制" class="copy" @click="copy">复制</div>
            <div title="剪切" class="cut" @click="cut">剪切</div>
            <div title="粘贴" class="paste" @click="paste">粘贴</div>
            <div title="删除" class="del" @click="del">删除</div>
            <div title="撤销" class="withdraw" @click="undo">撤销</div>
            <div title="重置" class="reset" @click="redo">恢复</div>
            <!-- <div title="保存" class="save" @click="save">保存</div> -->
            <div title="上传" class="upload" @click="upload">上传</div>
          </li>
        </ul>
        
      </div>
    </div>
    


    <!-- 弹窗 -->
    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      width="30%">
      <span>上传中</span>
    </el-dialog>
  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
// import ajax from "../../services";
import WaveSurfer from "wavesurfer.js";
import TimelinePlugin from "wavesurfer.js/dist/plugin/wavesurfer.timeline.min.js";
import RegionsPlugin from "wavesurfer.js/dist/plugin/wavesurfer.regions.min.js";
import Editor from "@/utils/editor";
import Bus from "./bus.js";
import axios from "axios";
// import Modal from "../TabContainer/Modal"
import basedialog from "../common/BaseDialog";
import {BASE_API_2} from '@/utils/url.js'
export default {
    data() {
        return {
        params: {
            vad: false,
            playPause: false,
            stop: false,
            toggleMute: false,
            vol: 1,
            zoomValue: 0,
            playRate: 1,
            rateStep: 0.25,
            canvasheight: document.body.clientHeight - 160
        },
        volumn: 1,
        screenWidth: document.body.clientWidth,
        isShowCircle: false,
        percent: 0,
        editorFilename: "",
        uploadData: {},
        dialogVisible: false
        };
    },
    computed: {
        getAudioPath() {
            return this.$store.state.audioPath;
        }
    },
    watch: {
        getAudioPath() {
            this.wavesurfer.destroy();
            this.showWavesurfer(this.$store.state.audioPath);
        }
    },
    methods: {
        ...mapActions(["AUDIO_PATH",'ORIGIN_VOICE','TRAIN_VOICE']),
        ...mapGetters(["getaudioPath"]),

        showWavesurfer(url) {
            let wavesurfer = WaveSurfer.create({
                container: "#waveform",
                cursorColor: "#999",
                audioRate: 1,
                scrollParent: true,
                backend: "WebAudio",
                barHeight: 1.5,
                waveColor: "#6983f4",
                progressColor: "#36a002",
                loaderColor: "#36a002",
                height: 200,
                plugins: [
                    TimelinePlugin.create({
                        container: "#waveform-timeline",
                        fontSize: 14,
                        primaryFontColor: "#9499df",
                        secondaryFontColor: "#9499df",
                        primaryColor: "#9499df",
                        secondaryColor: "#9499df"
                    }),
                    RegionsPlugin.create({})
                ]
            });
            wavesurfer.on("loading", (percents, target) => {
                // console.log(percents,target);
                this.isShowCircle = true;
                this.percent = percents;
                if (percents === 100) {
                    this.isShowCircle = false;
                }
            });
            wavesurfer.on("ready", percents => {
                this.wavesurfer.disableDragSelection();
                this.wavesurfer.enableDragSelection({ color: "rgba(0, 180, 0, 0.3)" });
            });
            wavesurfer.on("audioprocess", () => {
                // console.log("audioprocess");
            });
            wavesurfer.on("finish", () => {
                this.params.playPause = false;
                // console.log("finish");
            });

            document.querySelector("#waveform").addEventListener("mousedown", () => {
                this.wavesurfer.clearRegions();
            });
            this.wavesurfer = wavesurfer;

            // this.btnInit()

            // this.volumeBar();

            wavesurfer.load(url);

            // wavesurfer.load(url || "../../../static/audio/demo.wav");
            this.e = Editor.Editor.init(this.wavesurfer);

            // var regionList = this.wavesurfer.regions.list;
            //   console.log(regionList)
            // let buffer = this.wavesurfer.backend.buffer;
            //   console.log(buffer);
            // let data = [];
            // for (var i = 0; i < buffer.numberOfChannels; i++) {
            //       data.push(buffer.getChannelData(i));
            //   }
            //   console.log(data);
        },

        //按钮功能
        //播放/暂停按钮
        playPausebutton() {
            this.wavePlay();
        },
        //停止按钮
        stopbutton() {
            this.wavesurfer.stop();
            this.params.playPause = false;
        },
        //慢放
        skipBackwardbutton() {
            var playBackwardRate = this.wavesurfer.getPlaybackRate();
            this.params.playRate = playBackwardRate - this.params.rateStep;
            console.log(this.params.playRate);
            this.wavesurfer.setPlaybackRate(this.params.playRate);
            this.playRate();
        },
        //快放
        skipForwardbutton() {
            var playBackwardRate = this.wavesurfer.getPlaybackRate();
            this.params.playRate = playBackwardRate + this.params.rateStep;
            console.log(this.params.playRate);
            this.wavesurfer.setPlaybackRate(this.params.playRate);
            this.playRate();
        },
        //静音开关
        toggleMutebutton() {
            this.params.toggleMute = !this.params.toggleMute;
            if (!this.params.toggleMute) {
                this.wavesurfer.setVolume(1);
                this.volumn = 1;
            } else {
                this.wavesurfer.setVolume(0);
                this.volumn = 0;
            }
        },
        //音量条
        volumeBar() {
            this.wavesurfer.setVolume(this.params.vol);
        },
        //放大
        zoomInbutton() {
            if (this.params.zoomValue >= 100) {
                return;
            }
            this.params.zoomValue += 10;
            // console.log('放大')
            this.wavesurfer.zoom(this.params.zoomValue);
        },
        //缩小
        zoomOutbutton() {
            if (this.params.zoomValue <= 0) {
                return;
            }
            this.params.zoomValue -= 10;
            // console.log("缩小");
            this.wavesurfer.zoom(this.params.zoomValue);
        },
        //鼠标滚轮控制放大、缩小功能
        zoom(e) {
            if (e.deltaY < 0) {
                this.zoomInbutton();
            } else {
                this.zoomOutbutton();
            }
        },

        initWavesurferStatus() {
            var wavesurfer = this.wavesurfer;
            if (wavesurfer.isMuted) {
                wavesurfer.toggleMute();
            }
        },

        toSecond(str) {
            var Temp = str.split(":");
            var Seconds = 3600 * Number(Temp[0]) + 60 * Number(Temp[1]) + Number(Temp[2]);
            return Seconds;
        },

        //播放、暂停
        wavePlay(start, end) {
            var wavesurfer = this.wavesurfer;
            if (!wavesurfer.isPlaying()) {
                wavesurfer.play(start, end);
                this.params.playPause = true;
                //   if(this.params.displayTime){
                // 	clearInterval(this.params.displayTime)
                // }
            } else {
                wavesurfer.playPause();
                // this.ifPlay()
                //同步显示功能
                // syncDisplay(data);
                this.params.playPause = false;
            }
        },
        //加色块
        waveRegion(start, end, color, clear) {
            if (!clear) {
                this.wavesurfer.clearRegions();
            }

            this.wavesurfer.addRegion({
                start: start,
                end: end,
                color: color,
                drag: false
            });
        },

        playRate() {
            this.$Message.info({
                content: this.params.playRate + "倍"
            });
        },
        uploadMessage() {
            this.$message({
                message: "上传成功",
                type: "success",
                duration: 2000
            });
        },
        cancelMessage() {
            this.$message({
                message: "取消上传",
                type: "warning",
                duration: 2000
            });
        },

        cut() {
            this.e.cut();
            // this.wavesurfer.clearRegions()
            // console.log('this.e.clipboard')
            // console.log(this.e.cut());
        },
        copy() {
            // this.wavesurfer.clearRegions()
            this.audioData = this.e.copy();
            console.log(this.audioData);
        },
        paste() {
            // this.wavesurfer.clearRegions()
            this.e.paste();
        },
        del() {
            // this.wavesurfer.clearRegions()
            this.e.del();
        },
        redo() {
            // this.wavesurfer.clearRegions()
            this.e.redo();
        },
        undo() {
            // this.wavesurfer.clearRegions()
            this.e.undo();
        },
        upload() {
            // let uploadData = null;
            // alert(2)
            if (!this.uploadData.User) { // created()里每次都会预先给uploadData赋值, 但初始态仅含__ob__与__proto__
                this.$message({ type: 'error', message: '请先导入语音后, 再上传!' });
                return;
            }
            this.dialogVisible = true;
            // console.log('this.uploadData__upload()',this.uploadData);
            // console.log('%cthis__in__upload():','color:red;font-style:italic',this)
            let tmp = this.uploadData.SrcPath;
            let path = tmp.slice(tmp.lastIndexOf("/") + 1) + "_train.wav";
            let user = this.uploadData.User;
            // value.slice(value.lastIndexOf("/")+1);
            // console.log(path);
            // return
            let formData = new FormData();
            // let target = null;
            // for ( let k in this.uploadData ) {
            //   formData.append(k,this.uploadData[k]);
            // }
            this.audioData = this.e.save();
            let blob = this.createWavBlob();
            // var a = document.createElement("a");
            // a.href = window.URL.createObjectURL(blob);
            // a.innerHTML = "upload";

            // 指定生成的文件名
            // a.upload = "demo";
            // document.body.appendChild(a)
            // a.click();
            // window.URL.revokeObjectURL(a.href);
            // console.log(blob)
            formData.append("Fmt", "pcm");
            formData.append("Rate", 48000);
            formData.append("Bits", 16);
            formData.append("File", blob);
            formData.append("FileName", path);
            formData.append("Type", 1);
            formData.append("Policy", 1);
            formData.append("Channels", 1);
            formData.append("User", user);
            // return;
            axios({
                url: `${BASE_API_2}/api/sample`,
                method: "post",
                data: formData
            })
            .then(response => {
                // console.log(response.data);
                let res = response.data;
                if (res.code === "200") {
                    // this.dialogVisible = false;
                    // console.log(`${this.User}`)
                    axios({ url: `${BASE_API_2}/api/samples/${user}/1` }).then(res => {
                        // console.log(res.data);
                        // let res = 
                        // this.ORIGIN_VOICE(response.data.msg);
                        if (res.data.code === "200") {
                            this.$message({ type: "success", message: "操作成功!" });
                            // window.reload();
                            // this.$router.go(0);
                            this.dialogVisible = false;
                            let tmp = res.data.msg;
                            // this.User = row.User;
                            if (!tmp) return;
                            // this.originfileData = tmp.filter( (e) => { return e.Type === 0 });
                            // this.trainfileData = tmp.filter( (e) => { return e.Type === 1 });
                            // console.log(this.originfileData);
                            // console.log(this.trainfileData);
                            console.log(tmp.filter( (e) => { return e.Type === 0 }));
                            // console.log("this",this);
                            this.ORIGIN_VOICE(
                                tmp.filter(e => {
                                return e.Type === 0;
                                })
                            );
                            this.TRAIN_VOICE(
                                tmp.filter(e => {
                                    return e.Type === 1;
                                })
                            );
                        }
                    });
                }
            });
            return;
            // console.log(this.wavesurfer.backend.buffer)
            // var arraybuffer = this.wavesurfer.backend.buffer.getChannelData(0)
            // var blob = new Blob([arraybuffer]);

            // console.log(this.audioData)

            var a = document.createElement("a");
            a.href = window.URL.createObjectURL(blob);
            a.innerHTML = "upload";

            // 指定生成的文件名
            a.upload = "demo";
            // document.body.appendChild(a)
            a.click();
            window.URL.revokeObjectURL(a.href);

            // this.wavesurfer.loadBlob(blob)
        },
        save() { // this.e.save() 与 this.save()是不同的2个方法; upload()里调的不是这个方法
            // console.log('%cthis.$route:','color:green;font-weight:700;font-style:italic',this.$route);
            this.audioData = this.e.save();
            var blob = this.createWavBlob();
            let fileid = this.$route.params.fileid;
            let taskname = this.$route.params.taskname;
            let formData = new FormData();
            formData.append("file", blob);
            formData.append("remark", "123");
            formData.append("name", taskname);
            formData.append("taskid", fileid);
            formData.append("cropname", this.editorFilename);
            // return;
            this.$store.commit("save", fileid);
            // console.log(fileid)
            axios({ // 原为 @汤 封装的 ajax
                method: "post",
                url: `${BASE_API_2}/callvoice/savevoice/` + fileid,
                // emulateJSON: true,
                data: formData
            }).then(res => {
                console.log("res_in_save @ Audio's axios",res);
            });
        },
        createWavBlob() {
            var buf = this.wavesurfer.backend.buffer;

            // var data = buf.getChannelData(0);
            var data = this.audioData[0];
            console.log(data);
            var wav = this.encodeWAV(data, buf.sampleRate);
            console.log("wav---------------",buf.sampleRate)
            var blob = new Blob([wav], { type: "audio/wav" });
            return blob;
        },
        writeString(view, offset, string) {
            for (var i = 0; i < string.length; i++) {
                view.setUint8(offset + i, string.charCodeAt(i));
            }
        },
        floatTo16BitPCM(output, offset, input) {
            for (var i = 0; i < input.length; i++, offset += 2) {
                var s = Math.max(-1, Math.min(1, input[i]));
                output.setInt16(offset, s < 0 ? s * 0x8000 : s * 0x7fff, true);
            }
        },
        encodeWAV(samples, sampleRate) {
            // alert(sampleRate)
            // return;
            // debugger
            var writeString = this.writeString;
            var floatTo16BitPCM = this.floatTo16BitPCM;

            var buffer = new ArrayBuffer(44 + samples.length * 2);
            var view = new DataView(buffer);

            /* RIFF identifier */
            writeString(view, 0, "RIFF");
            /* file length */
            view.setUint32(4, 32 + samples.length * 2, true);
            /* RIFF type */
            writeString(view, 8, "WAVE");
            /* format chunk identifier */
            writeString(view, 12, "fmt ");
            /* format chunk length */
            view.setUint32(16, 16, true);
            /* sample format (raw) */
            view.setUint16(20, 1, true);
            /* channel count */
            view.setUint16(22, 1, true);
            /* sample rate */
            view.setUint32(24, sampleRate, true);
            /* byte rate (sample rate * block align) */
            view.setUint32(28, sampleRate * 2, true);
            /* block align (channel count * bytes per sample) */
            view.setUint16(32, 2, true);
            /* bits per sample */
            view.setUint16(34, 16, true);
            /* data chunk identifier */
            writeString(view, 36, "data");
            /* data chunk length */
            view.setUint32(40, samples.length * 2, true);

            floatTo16BitPCM(view, 44, samples);

            return view;
        },
        goBack() {
            this.$router.go(-1);
            this.wavesurfer.destroy();
        },
        handleRender() {
            this.$Modal.confirm({
                render: h => {
                    return h("Input", {
                        props: {
                            value: this.editorFilename,
                            autofocus: true,
                            placeholder: "请输入编辑语音的名称"
                        },
                        on: {
                            input: val => {
                                this.editorFilename = val;
                            }
                        }
                    });
                },
                onOk: () => {
                    this.save();
                }
            });
        },
        onClick(e) {
            console.log(e);
        },
        handleClose(done) {
            this.$confirm('确认关闭？')
                .then(_ => {
                    done();
                })
                .catch(_ => {});
        },
    },
    created() {
        Bus.$on("getTarget", target => {
            // console.log(target)
            this.uploadData = target;
            //   console.log('%cthis.uploadData:','color:red',this.uploadData)
        });
    },
    mounted() {
        let path = this.$store.state.audioPath;
        // console.log("audio接收的数据",path);
        this.showWavesurfer(path);
    },
};
</script>
<style lang="less" scoped>
@import "../../assets/less/var.less";
.editor {
  // height: 100%;
  // padding: 40px 0;
  .title {
    width: 100%;
    height: 40px;
    background-color: #373842;
    box-shadow: 0px 3px 7px 0px rgba(0, 0, 0, 0.35);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    color: #9395a2;
    position: absolute;
    top: 0;
    left: 0;
  }
  .graph {
    height: 100%;
    width: 100%;

    .musk {
    }
  }
}
.wave {
  width: 100%;
  // height: 100%;
  background-color: #303036;
}
.wave-controll {
  width: 100%;
  height: 40px;
  background-color: #40414b;
  // position: absolute;
  // bottom: 0;
  > ul {
    display: flex;
    height: 40px;
    justify-content: space-between;
    align-items: center;
    .left {
      display: flex;
      justify-content: space-between;
      > div {
        width: 40px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        margin-right: 1px;
        border-right: 1px solid #9395a2;
        border-left: 1px solid #9395a2;
        font-size: 12px;
        color: #9395a2;
        &.skip_backward {
          ._bg_img("慢放0");
          border-left: none;
          &:hover {
            ._bg_img("慢放1");
          }
        }
        &.play_pause {
          ._bg_img("暂停0");
          &:hover {
            ._bg_img("暂停1");
          }
          &.on {
            ._bg_img("播放0");
          }
        }
        &.skip_forward {
          ._bg_img("快放0");
          &:hover {
            ._bg_img("快放1");
          }
        }
        &.stop {
          ._bg_img("停止0");
          &:hover {
            ._bg_img("停止1");
          }
        }
        &.sound {
          ._bg_img("音量0");
          border-right: none;
          &:hover {
            ._bg_img("音量1");
          }
          &.on {
            ._bg_img("静音0");
            &:hover {
              ._bg_img("静音1");
            }
          }
        }
        &.volumn {
          width: 100px;
          border-left: none;
          line-height: 35px;
          > input {
            -webkit-appearance: none;
            -moz-appearance: none;
            -ms-appearance: none;
            width: 80px;
            height: 2px;
            background-color: #bbbbbb;
            &::-webkit-slider-thumb {
              -webkit-appearance: none;
            }
            &::-moz-range-trackpseduo {
              -moz-appearance: none;
            }
            &::-ms-track {
              width: 100%;
              cursor: pointer;
              background: transparent; /* Hides the slider so custom styles can be added */
              border-color: transparent;
              color: transparent;
            }
            &:focus {
              outline: none;
            }
            &::-webkit-slider-thumb {
              -webkit-appearance: none;
              height: 9px;
              width: 9px;
              margin-top: -1px;
              background: #bbb;
              border-radius: 50%;
              border: solid 0.125em rgba(205, 224, 230, 0.5);
            }
            &::-moz-range-thumb {
              -moz-appearance: none;
              height: 6px;
              width: 6px;
              margin-top: -1px;
              background: #bbb;
              border-radius: 50%;
              border: solid 0.125em rgba(205, 224, 230, 0.5);
            }
            &::-ms-track {
              -moz-appearance: none;
              height: 6px;
              width: 6px;
              margin-top: -1px;
              background: #bbb;
              border-radius: 50%;
              border: solid 0.125em rgba(205, 224, 230, 0.5);
            }
          }
        }
        &.zoom_in {
          ._bg_img("lachang");
          &:hover {
            ._bg_img("音轨拉长1");
          }
        }
        &.zoom_out {
          ._bg_img("suoduan");
          &:hover {
            ._bg_img("音轨缩短1");
          }
        }
      }
    }
    .right {
      display: flex;
      justify-content: space-between;
      > div {
        width: 40px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        margin-right: 1px;
        border-right: 1px solid #9395a2;
        border-left: 1px solid #9395a2;
        font-size: 12px;
        color: #9395a2;
        &.cut {
          // ._bg_img("jq01");
          &:hover {
            // ._bg_img("jq02");
          }
