<template>
	
	<div class='main'>
		<div class="myhead">
            <!-- <div v-bind:style="{backgroundColor:'pink',width:'100px',height:'100px',backgroundImage:'url(../assets/logo.png)'}">
            </div> -->
            <span v-bind:style="{backgroundColor:'#6e8de3',width:'130px',height:'40px'}">
                <img class='logoImg' src='@/assets/logo.png' />
            </span>
			<div class="rightNav">
				<ul>
					
					<li @click="passwordHandle">{{account}}</li>
					<router-link class='myApplication pointer' tag='li' to="/login">退出</router-link>
				</ul>
			</div>
		</div>
		<router-view></router-view>		
	</div>

</template>

<script>
import {mapActions,mapGetters} from 'vuex'

export default{
	name: 'myhead',
		data() {
    		return {
				account: '',
    			activeIndex: '1',
    		}
  		},
  	components: {
    
    // 'password-dialog': passwordDialog // 此组件未写
  	},
  	methods:{
  		...mapGetters(['getAccount']),
  		handleSelect(key, keyPath) {
      		console.log('Key & KeyPath: ',key, keyPath);
    	},
	  	passwordHandle()  {
	  		this.$refs.c1.showDialog()
	  	},
	  	childCreate(params) {
	  		userChangePassword(params).then(res => {
        		console.log('%cres','color:red',res)
        		// this.dataInit()
        		if(!res.data.success){
        			this.$message.error(res.data.message)	
        		}else{
        			this.$message({
	          			message: res.data.message,
	          			type: 'success'
	        		})
        		}
      		})
	  	}
  	},
  	created() {
		  this.account = this.getAccount()  /* store/getter.js导出了getAccount常量;
											   myApplication/index.vue从api/index.js里导入了getAccount;
											   router/index.js里是先引入myhead.vue, 再引入其它如查询页之类的页面 */
  	}
}
</script>
<style>
   .logoImg {
       position: absolute;
       top: 15px;
        left: 25px;
   }
</style>

<style lang="scss">
	.myhead{
		ul,li{
			list-style: none;
		}
		
		.el-menu-demo{
			margin-left: 1000px;
			width: 100%;
			li.el-menu-item{
				width: 200px;
			}
			.myApplication{
				// width: 200px;
				// margin-left: 1000px;

			}
			.set{
				// width: 200px;
				// margin-left: 1000px;
			}
		}
	}
	
</style>
<style scoped lang="scss">
	.main{
		height: 100%;
	}
	.myhead{

		height: 60px;
		width: 100%;
		// width: calc(100% - 220px);
		background-color: #6e8de3;
		display: flex;
		line-height: 60px;

		// .myApplication{
		// 	margin-left: auto;
		// 	margin-right: 20px;
		// 	font-size: 30px;
		// }
		// .set{
		// 	margin-right: 20px;
		// 	font-size: 30px;
		// }
		justify-content: flex-end;
		.rightNav{
			ul{
				display: flex;
				font-size: 14px;
				color: #fff;
				li{
					position: relative;
					margin: 0 20px;
					&:after{
						content: '';
						width: 1px;
						height: 20px;
						background-color: #fff;
						display: block;
						position: absolute;
						top: 20px;
						right: -20px;
					}
					// &:nth-child(1){
					// 	font-size: 24px;
					// }
					// &:nth-child(3)::after{
					// 	display: none;
					// }
					&:nth-last-child(1)::after{
						display: none;
					}
				}
			}
		}
	}
	.pointer {
		cursor: pointer
	}
</style>


