                let tmp = res.data.msg;
                this.User = row.User;
                if (!tmp) return;
                // this.originfileData = tmp.filter( (e) => { return e.Type === 0 });
                // this.trainfileData = tmp.filter( (e) => { return e.Type === 1 });
                // console.log(this.originfileData);
                // console.log(this.trainfileData);
                // console.log(tmp.filter( (e) => { return e.Type === 0 }));
                this.ORIGIN_VOICE(tmp.filter( (e) => { return e.Type === 0 }));
                this.TRAIN_VOICE(tmp.filter( (e) => { return e.Type === 1 }))
            }
        });
        // console.log(this.$refs.cl) 
        this.$refs.cl.closeBtn();
    },
    handleDownLoad(index, row) {
      // console.log(row);row中包括行的所有信息
      console.log("download", index);
    },
    handleEdit(index, row) {
      console.log("edit", index);
    },
    handleDelete(index, row) {
        console.log("delete", row.User);
        axios({
            url: `${BASE_API_2}/api/objects/${row.User}`,
            methods: "delete"
        })
        .then(res => {
            // console.log(res.data);
            // if (res.data.ok) {
            //   this.loadData();
            // }
        });
    },
    handleSelectionChange(val) {
      // console.log(selection);
    //   console.log(val); //选中状态改变的时候触发
      this.multipleSelection = val;
      // event.cancelBubble = true;
      // console.log("1")
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]));
    },
    export2Excel() {
        // require.ensure([], () => {
        //     const { export_json_to_excel } = require("../../vendor/Export2Excel");
        //     const tHeader = [
        //     "序号",
        //     "声纹编号",
        //     "声纹名称",
        //     "语种",
        //     "性别",
        //     "类型",
        //     "训练状态",
        //     "中标阈值",
        //     "上线状态",
        //     "创建时间",
        //     "备注",
        //     "操作"
        //     ];
        //     const filterVal = [
        //     "id",
        //     "voiceNo",
        //     "voiceName",
        //     "lang",
        //     "gender",
        //     "type",
        //     "trainStatus",
        //     "threshold",
        //     "onlineStatus",
        //     "createTime",
        //     "record"
        //     ];
        //     const list = this.tableData;
        //     //　　filterVal是data中list的key值，也是要自己写的
        //     const data = this.formatJson(filterVal, list);
        //     // 　这里可定义导出的excel文件名(声纹管理列表)
        //     export_json_to_excel(tHeader, data, "声纹管理列表");
        // });
    },
    handleSelect() {
      console.log("danji");
    },
    slidePanel(flag) {
        // console.log(flag);
        let table = document.querySelector(".voiceprint-list");
        // let transitionFlag = true;
        flag
            ? (table.style.width = "calc(100% - 340px)")
            : (table.style.width = "100%");
    },
    showMedia(flag) {
        // console.log('isShowMedia:',flag)
        this.isShowMedia = flag;
    },
    loadFileData(id) {
        console.log(this.$route.params.id);
        axios({
            url: `${BASE_API_2}/api/objects/` + id,
            method: "get"
            //"http://localhost:9000/api/getfilelist"
        })
        .then(res => {
            console.log(res.data.result);
            this.fileData = res.data.result;
        });
    },
    plus() {
        this.dialog_form.Threshold++;
    },
    minus() {
        this.dialog_form.Threshold--;
    }
  },
    created() {
        // console.log("http://${BASE_API_2}/api/objects: ",`http://${BASE_API_2}/api/objects`)
        this.loadData();
        // this.loadDataTest();
        // this.loadFileData(this.$route.params.id);
    },
    watch: {
        $route: function() {
            console.log("SpeakerManage的路由变化了");
            this.loadFileData(this.$route.params.id);
        },
        // tmpHei(newVal, oldVal) {
        //     console.log('%chei_newVal','color:green;font-weight:700',newVal);
        //     this.reaHei = newVal + 'px';
        // }  // step 2.
    },
    updated(){
        // this.tmpHei = document.querySelector('.table_content').clientHeight; // step 3.
        /* 4个步骤: 另2个是--> step 0:在data()那里定义 tmpHei和reaHei;
                              step 1:在<el-table>里写:style="{'max-height': this.reaHei}" */
        /* 在<el-table>里无论写 :style="{'max-height': this.reaHei}" 还
           是 :style="{'height': this.reaHei}" , 而不写
           height= "-webkit-fill-available" , 都会导致不产生滚动条!!! */
    },
    beforeRouteLeave (to, from, next) {
        this.$refs.media.wavesurfer.stop();
        this.$refs.media.wavesurfer.destroy();
        // this.$refs.media.showWavesurfer('');
        this.$refs.media.params.playPause = false;
        next(); // 必须有这个，相当于一个按钮开启一样。
    }
};
</script>

<style type="text/css" scoped>
.h3 {
    font-size: 16px;
    position: relative;
    top: -18px;
}
.el-table th.is-leaf {
    border-bottom: 1px solid #c2c2cf;
}
.el-table--border th {
    border-right: 1px solid #c2c2cf;
}
.el-pagination {
    padding: 0;
}
.el-table--scrollable-y .el-table__body-wrapper {
    overflow-y: auto;
}
.el-table--scrollable-x .el-table__body-wrapper {
    overflow-x: auto;
}
</style>


<style lang="scss">
// @import "../../assets/less/var.less";
// @import url("//unpkg.com/element-ui@2.4.0/lib/theme-chalk/index.css");
// .el-select .el-input {
//     width: 130px;
//   }
//   .input-with-select .el-input-group__prepend {
//     background-color: #fff;
//   }
.success {
  color: #4cbf40;
}
.error {
  color: #f17f7f;
}
.yellow {
  color: #ffa426;
}
.el-select .el-input {
  width: 120px;
}
.input-with-select .el-input-group__prepend {
  background-color: #fff;
}
.el-icon-document {

    &:hover {
        color: #6e8de3;
        cursor: pointer;
    }
}
.el-table-delete {
    display: inline-block;
    width: 45%;
    background: no-repeat url('../../../../../static/images/删除默认_18x18.png');
    // background-size: 22%;
    background-position: 4px 2px;
    padding-left: 12px;
    &:hover {
        color: #ff5e52;
        cursor: pointer;
        background: no-repeat url('../../../../../static/images/表格删除选中_18x18.png');
        background-position: 4px 2px;
    }
}
.el-table-edit {
    display: inline-block;
    width: 45%;
    background: no-repeat url('../../../../../static/images/编辑默认_18x18.png');
    // background-size: 22%;
    background-position: 4px 2px;
    padding-left: 12px;
    &:hover {
        color: #6e8de3;
        cursor: pointer;
        background: no-repeat url('../../../../../static/images/表格编辑选中_18x18.png');
        background-position: 4px 2px;
    }
}
// .el-icon-delete {
//   &:hover {
//     color: #6e8de3;
//     cursor: pointer;
//   }
// }
// .el-icon-edit {
//   &:hover {
//     color: #6e8de3;
//     cursor: pointer;
//   }
// }
.voiceprint-list {
  width: 100%;
  height: 100%;
  display: flex;
  // background-color: #e5e5ef;
//   padding: 0px 0px 20px 20px; // 0620 注掉, 不然与 @陈 的样式冲突
  .wrapper {
    width: 100%; // 0620 从原先的 100% 改为 inherit, 不然与 @陈 的样式冲突
    height: 100%;
    background-color: #fff;
    border-radius: 5px;
    padding: 30px;
    position: relative;
    .title {
      display: flex;
      justify-content: space-between;
      height: 46px;
      line-height: 46px;
      margin-bottom: 20px;
      .flag {
        background: linear-gradient(90deg, skyblue, skyblue 60%, white);
        border-radius: 5px;
      }
      .btns {
        color: #409EFF;
        border-color: #409EFF;
        background-color: #ecf5ff;
        margin-left: 10px;
        height: 30px;
        font-size: 14px;
        &.el-button.is-round.el-button.is-round {
          padding: 8px 23px;
        }
        // line-height: 20px;
        &:hover {
          color: #fff;
          background-color: #409EFF;
          border-color: #409EFF;
        }
        &:active {
          background: #3a8ee6;
          border-color: #3a8ee6;
          color: #fff;
        }
      }
    }
    .table_content {
      .el-table {
      font-size: 12px;

      }
      .voice-page {
        text-align: right;
        margin-top: 20px;
      }
    }
    .media {
      width: 100%;
      position: absolute;
      bottom: 0;
      left: 0;
    }
  }
}
</style>







