<template>
  <div class="player" style='z-index: 1'>
    <div class="header"></div>
  	<div class="wave">
      <div id="waveform"></div>
      <div id="timeline"></div>
    </div>
    <div class="wave_controll">
      <ul ref="btns">
        <li id="skipBackwardbutton"  title="慢放"></li>
        <li id="playPausebutton" :class="{ 'on' : params.playPausebutton }" title="播放/暂停"></li>
        <li id="skipForwardbutton"  title="快放"></li>
        <li id="stopbutton" :class="{ 'on' : params.stopbutton }" title="停止"></li>
        <li id="toggleMutebutton" :class="{ 'on' : params.toggleMutebutton }" class="sound"></li>
        <li id="sound">
          <input id="volButton" type="range" min="0" max="1" value="0.8" step="0.01" />
        </li>
        <li id="big" title="放大"></li>
        <li id="small" title="缩小"></li>
<!--         <li @click="vad" id="vad" :class="{ 'on' : params.vad }"></li>
        <li id="pathA" data-status="off"></li>
        <li id="pathB" data-status="off"></li> -->
        <li class='editBlock'>
            <span class='edit' title="复制"  @click="copy">复制</span>
            <span class='edit' title="剪切"  @click="cut">剪切</span>
            <span class='edit' title="粘帖"  @click="paste">粘帖</span>
            <span class='edit' title="删除"  @click="del">删除</span>
            <span class='edit' title="撤销"  @click="undo">撤销</span>
            <span class='edit' title="重做"  @click="redo">重做</span>
            <span class='edit' title="上传"  @click="upload">上传</span>
        </li>
        <li id="downLoad"  @click="downloadVoice()"></li>
      </ul>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import {mapActions,mapGetters} from 'vuex'
import WaveSurfer from 'wavesurfer.js';
import TimelinePlugin from 'wavesurfer.js/dist/plugin/wavesurfer.timeline.min.js';
import RegionsPlugin from 'wavesurfer.js/dist/plugin/wavesurfer.regions.min.js'
import {BASE_API, BASE_API_2} from '@/utils/url.js' // [1][发布用] 0607打包时要注释掉
import Editor from '@/utils/editor.js'
import {uploadTrainFile, getSomeoneTrainFiles} from '@/api/index.js'
// import mp3 from '../../../static/107.wav'
export default {
    name: 'player',
    data () {
        return {
            params:{
                vad: false,
                playPausebutton: false,
                stopbutton: false,
                toggleMutebutton: true,
                zoomValue: 0
            },
            audioPath:''
        }
    },
    props: {
  
    },
    computed: {
        getAudioPath(){
            return this.$store.state.audioPath
        }
    },
    watch: {
        getAudioPath(){
            // alert('watch @ player.vue');
            this.audioPath = this.getaudioPath()
            console.log("%cthis.audioPath @ watch", 'color:pink', this.audioPath) //相对路径
            
            // this.loadAudio(`${this.audioPath}`) // [2][发布用] 正式环境下用这行,因与后台程序放在一起,故上面的相对路径就变成了绝对路径
            this.loadAudio(`${BASE_API_2}${this.audioPath}`)
            // this.loadAudio(`http://${BASE_API}${this.audioPath}`) // [2] 实际音频的完整路径; 测试时用这个,正式时注掉; ES6的模版字符串(Template Strings)
            // this.loadAudio('http://192.168.1.243:10001/api/pcm/1529061343271379900_20180615_191501_0_1886/man/20180615/20180615_191501_0_1529061343_201804271620.wav')
            // this.loadAudio('http://192.168.1.73:8088/admin/voices/alaw/13315634567/testA1.wav') // 测试用路径
        }
    },
    methods:{
        ...mapActions(['AUDIO_PATH']),
        ...mapGetters(['getaudioPath']),
        //wavesurfer初始化 flag:标记无效音
        showWavesurfer (flag = false) {
            console.log(flag)
      
            //创建wavesurfer实例
            let wavesurfer = WaveSurfer.create({
                container: '#waveform',
                cursorColor: '#999',
                audioRate:1,
                scrollParent: true,
                backend: 'WebAudio',
                barHeight: 1.5,
                waveColor     : '#6983f4',
                progressColor : '#36a002',
                loaderColor   : '#36a002',
                hideScrollbar : false, // 这会让波形在窗口改变大小后, 不随之变动, 而凭添一个滚动条; 除非改变滚动条样式, 否则极丑
                // hideScrollbar: true,
                responsive: true,
                autoCenter    : true,
                height:128,
                plugins: [
                    TimelinePlugin.create({
                        container: '#timeline',
                        fontSize: 14,
                        primaryFontColor: "#9499df",
                        secondaryFontColor: "#9499df",
                        primaryColor: "#9499df",
                        secondaryColor: "#9499df"
                    }),
                    RegionsPlugin.create({
                      // plugin options ...
                    })
                ]
            });
            // wavesurfer.load('../../../static/wewr-zhy-test-01.wav')
            // wavesurfer.load('http://45.78.60.202:3001/【汉】语_中标固定音频2_白杨礼赞.wav')
            // wavesurfer.load('http://192.168.1.108:9000/api/vocs/pcm/13681816122/B (8).wav')
            //
            //
            //
            this.wavesurfer = wavesurfer;
            // console.log('%c---- wavesurfer ----:','color:blue;font-weight:700',this.wavesurfer)
            //初始化按钮
            this.btnInit();

            /* 0619 编辑功能初始化 */
            this.e = Editor.Editor.init(this.wavesurfer);
      
            // this.getData()
            // wavesurfer.load(mp3)  /* 根据地址载入音频 */
            // console.log('%cthis @ Player.vue:','color:red',this)
            // if (BASE_API) console.log('%c${BASE_API}', 'color:#87a96b', BASE_API)
            // console.log("%cthis.audioPath @ methods.showWavesurfer:", 'color:#FF1493', this.audioPath)
            
            // this.loadAudio('http://192.168.1.73:8088/admin/voices/alaw/13315634567/testA1.wav'); /* 测试用数据 */
      
      
      
            //无效音跳过
            if(!flag){
              // this.showKeyWords()
              // this.timeArrInit()
            }
            //加载完成即刻播放
            wavesurfer.on('ready', ()=> {
              this.wavePlay()
              this.wavesurfer.zoom(0);
              // 0619 Enable Regions
                if (wavesurfer.enableDragSelection) {
                    wavesurfer.enableDragSelection({
                        color: 'rgba(0, 255, 0, 0.1)'
                    });
                }
            })

            // 0619 将finish event移至此处
            wavesurfer.on('finish',() => {
                this.params.playPausebutton = false
            })
      
            // wavesurfer.on('audioprocess', (p)=>{
            //   console.log(p)
            // })
        },
        //初始化按钮
        btnInit () {
    
            const defaultRate = 1, perRate = 0.25, defaultVol =  0.5;
      
            //慢放
            document.getElementById("skipBackwardbutton").onclick = () => {
                let playBackwardRate = this.wavesurfer.getPlaybackRate()
                if(this.wavesurfer.getPlaybackRate() == .5){
                    this.$message({
                        message: this.params.playRate+"倍",
                        duration: 500
                    })
                    return
                }
        
                this.params.playRate = playBackwardRate - perRate
                this.wavesurfer.setPlaybackRate(this.params.playRate)
        
                this.$message({
                    message: this.params.playRate+"倍",
                    duration: 500
                })
                console.log('skipBackwardbutton')
            }
      
            //快放
            document.getElementById("skipForwardbutton").onclick = () => {
                let playBackwardRate = this.wavesurfer.getPlaybackRate()
        
                if(this.wavesurfer.getPlaybackRate() == 4){
                    this.$message({
                        message: this.params.playRate+"倍",
                        duration: 500
                    })
                    return
              }
      
              this.params.playRate = playBackwardRate + perRate
              this.wavesurfer.setPlaybackRate(this.params.playRate)
      
              this.$message({
                  message: this.params.playRate+"倍",
                  duration: 500
              })
      
            }
      
            //播放/暂停
            document.getElementById("playPausebutton").onclick = () => {
                this.wavePlay()
            }
      
            //停止
            document.getElementById("stopbutton").onclick = () => {
                this.params.playPausebutton = false
                this.wavesurfer.stop();
                this.wavesurfer.play(0,.01);
                this.initWavesurferStatus();
            }
      
            //静音开关
            document.getElementById("toggleMutebutton").onclick = () => {
                if(this.params.toggleMutebutton){
                    this.wavesurfer.setVolume(0)
                    this.params.toggleMutebutton = false
                    document.getElementById("volButton").value = 0
                }else{
                    this.wavesurfer.setVolume(1)
                    this.params.toggleMutebutton = true
                    document.getElementById("volButton").value = 1
                }
            }
            //放大 缩小
            document.getElementById("big").onclick = () => {
                if (this.params.zoomValue >= 100) return;
                this.params.zoomValue += 10
                this.wavesurfer.zoom(this.params.zoomValue)
            }
      
            document.getElementById("small").onclick = () => {
                if (this.params.zoomValue <= 0) return;
                this.params.zoomValue -= 10
                this.wavesurfer.zoom(this.params.zoomValue)
            }
      
            //滚轮控制放大、缩小功能
            document.getElementById("waveform").onmousewheel = (e) => {
                console.log('滚轮e: ',e)
                if(e.deltaY < 0){
                    document.getElementById("big").click()
                }else{
                    document.getElementById("small").click()
                }
            }
            //音量条
            this.volumeBar();

            /* 0619 点下鼠标,清除选中区域 */
            document.querySelector("#waveform").addEventListener("mousedown", () => {
                this.wavesurfer.clearRegions();
            });
        },
        // 0619 波形图区域选中功能
        waveRegion(start, end, color, clear) {
            if (!clear) {
                this.wavesurfer.clearRegions();
            }

            this.wavesurfer.addRegion({
                start: start,
                end: end,
                color: color,
                drag: false
            });
        },
        //播放/暂停
        wavePlay (start,end) {
            // this.params.playPausebutton = !this.params.playPausebutton
            // console.log(this.params.playPausebutton)
            //
            console.log('wavePlay')

            let wavesurfer = this.wavesurfer

            // wavesurfer.on('finish',() => {
            //     this.params.playPausebutton = false
            // })
            start==null?wavesurfer.playPause():wavesurfer.play(start,end)

            if(!wavesurfer.isPlaying()) {
                this.params.playPausebutton = false
                // this.ISPLAY(this.isplay)
                // if(this.params.displayTime){
                //   clearInterval(this.params.displayTime)
                // }

            }else{
                this.params.playPausebutton = true
                // this.ifPlay()
    
                this.isplay = true
                // this.ISPLAY(this.isplay)

            }
        },
        // ifPlay () {
        //     if(this.params.playTime){
        //         clearInterval(this.params.playTime)
        //     }
        //     this.params.playTime = setInterval(() => {
        
        //         if(!this.wavesurfer.isPlaying()){
        //             this.params.playPausebutton = false
        //             // this.ISPLAY(false)
        //             clearInterval(this.params.playTime)
        //         }
    
        //         // console.log('ifPlay')
        //     },100)
        // },
        initWavesurferStatus () {
            let wavesurfer = this.wavesurfer
            if(wavesurfer.isMuted){
                wavesurfer.toggleMute()
            }
        },
        //音量条
        volumeBar () {
            let flag = false
            let volButton = document.getElementById('volButton')
            volButton.onmousedown = () =>{
                flag = true
            }
            volButton.onmousemove = (e) =>{
                if(!flag) return;
                if(e.offsetX>=0 && e.offsetX<=80){
                  this.params.toggleMutebutton = true
                  this.wavesurfer.setVolume(e.offsetX/80)
                }else if(e.offsetX<0){
                  this.params.toggleMutebutton = false
                  this.wavesurfer.setVolume(0)
                }else{
                  this.wavesurfer.setVolume(1)
                }
            }

        },
        loadAudio (data) {
            console.log('loadAudio()_Entered !')
            console.log(data)
            // console.log(this.wavesurfer)
    
            // this.wavesurfer.load(this.resdata.voicefilespath[1])
            // console.log(data)
            // axios({
            //     methods: 'get',
            //     url: 'http://192.168.1.108:9000/api/vocs/pcm/13681816123/1.wav'
            // }).then((res)=>{
            
            //   // var b = new Blob(res.data)
            //   console.log(typeof res.data)
            //   console.log(res)
            //   // this.wavesurfer.loadBlob(res.data)
            // })
            //

            this.wavesurfer.load(data)
            // window.open(`${data}`)
            // console.log('%c>> data:','color:red' , data )
            // console.log('%c>> ${data}: ','color:blue',`${data}`)
        },
        cut() { 
            this.params.playPausebutton = false;  // 剪切时,播放图表改为暂停; 粘帖时不变  
            this.e.cut()
            
        },
        copy() {
            this.params.playPausebutton = false; // 复制时,播放图表改为暂停; 粘帖时不变
            this.audioData = this.e.copy();
            console.log('%cthis.audioData:','color:red', this.audioData);
        },
        paste() {
            this.e.paste()
        },
        del() {
            this.params.playPausebutton = false;
            this.e.del()
        },
        redo() {
            this.e.redo();
            this.params.playPausebutton = false; // redo & undo, 本行置于末尾才能确保图标是正确的
        },
        undo() {
            alert('undo----this.params.playPausebutton: '+this.params.playPausebutton)
            this.e.undo()
            this.params.playPausebutton = false;
        },
        writeString (view, offset, string) {
            for (var i = 0; i < string.length; i++) {
                view.setUint8(offset + i, string.charCodeAt(i));
            }
        },
        floatTo16BitPCM (output, offset, input) {
            for (var i = 0; i < input.length; i++, offset += 2) {
                var s = Math.max(-1, Math.min(1, input[i]));
                output.setInt16(offset, s < 0 ? s * 0x8000 : s * 0x7FFF, true);
            }
        },
        encodeWAV (samples, sampleRate) {
            var writeString = this.writeString
            var floatTo16BitPCM = this.floatTo16BitPCM

            var buffer = new ArrayBuffer(44 + samples.length * 2);
            var view = new DataView(buffer);

            /* RIFF identifier */
            writeString(view, 0, 'RIFF');
            /* file length */
            view.setUint32(4, 32 + samples.length * 2, true);
            /* RIFF type */
            writeString(view, 8, 'WAVE');
            /* format chunk identifier */
            writeString(view, 12, 'fmt ');
            /* format chunk length */
            view.setUint32(16, 16, true);
            /* sample format (raw) */
            view.setUint16(20, 1, true);
            /* channel count */
            view.setUint16(22, 1, true);
            /* sample rate */
            view.setUint32(24, sampleRate, true);
            /* byte rate (sample rate * block align) */
            view.setUint32(28, sampleRate * 2, true);
            /* block align (channel count * bytes per sample) */
            view.setUint16(32, 2, true);
            /* bits per sample */
            view.setUint16(34, 16, true);
            /* data chunk identifier */
            writeString(view, 36, 'data');
            /* data chunk length */
            view.setUint32(40, samples.length * 2, true);

            floatTo16BitPCM(view, 44, samples);

            return view;
        },
        createWavBlob () {
            var buf = this.wavesurfer.backend.buffer;
            var data = this.audioData[0]

            var wav = this.encodeWAV(data, buf.sampleRate);
            var blob = new Blob([wav], {type: 'audio/wav'});
            return blob;
        },
        upload() {
            this.audioData = this.e.save()
            var blob = this.createWavBlob();
            let playingFileName = this.$store.state.audioPath;
            let trainFileName = playingFileName.slice(playingFileName.lastIndexOf("/") + 1) + "_train.wav";
            let user = this.$store.state.account;
            
            let formData = new FormData();
            formData.append("Fmt", "pcm");
            formData.append("Rate", 48000);
            formData.append("Bits", 16);
            formData.append("File", blob);
            formData.append("FileName", trainFileName);
            formData.append("Type", 1);
            formData.append("Policy", 1);
            formData.append("Channels", 1);
            formData.append("User", user);
            axios({
                url: `${BASE_API_2}`+ '/api/sample',
                method: "post",
                data: formData,
                headers: {'Content-Type': 'multipart/form-data'}
            })
            .then(res => {
                console.log('-+-+ res.data +-+- _uploadTrainFile:',res.data);
                if (res.data.code === "200") {
                    axios({
                        url: `${BASE_API_2}` + '/api/samples/' + user + '/1',
                        method: 'get',
                        data: ''
                    })
                    .then(res => {
                        if (res.data.code === "200") {
                            this.$message({ type: "success", message: "操作成功!" });
                        }
                    })
                    .catch(err => {
                        console.error('Error__getSomeoneTrainFiles:',err)
                    })

                }
            })
            .catch(err => {
                console.error('Error__uploadTrainFile:',err)
            })
            
        },
        downloadVoice () {
            // console.log('%cthis_downloadVoice @ player.vue:','font-weight:700',this)
            let url = `${BASE_API_2}`+this.audioPath; // [3] 正式环境换成下行
            // let url = this.audioPath; // 此为正式环境时所用 url
            
            /* 这种形式, Cr/Ff/IE都无效, IE额外还需要polyfill,参看https://segmentfault.com/a/1190000009703763
            var form=document.createElement("form");//定义form表单,通过表单发送请求
            form.setAttribute("style","display:none");//设置为不显示
            form.setAttribute("target","");
            form.setAttribute("method","get");//设置请求类型  
            form.setAttribute("action",url);//设置请求路径
            document.getElementsByTagName("body")[0].appendChild(form);//添加表单到页面(body)中
            form.submit();
            */
            // window.location.href='http://'+url; /* FF下无效 */

            var elemIF = document.createElement("iframe"); /* FF下无效 */
            elemIF.src = url;    
            elemIF.style.display = "none";     
            document.body.appendChild(elemIF); 

        },
        vad () {

        }
    },
    beforeCreate() {
  
    },
    created() {
        // console.log('%cplayer_this:','font-weight:700;color:aqua',this)
    },
    beforeMount() {
  
    },
    mounted() {
        let that = this
        let test = new Promise(function(){
          that.showWavesurfer()
        })
    },
    beforeUpdate() {
  
    },
    updated() {
  
    },
    activated() {
  
    },
    deactivated() {
  
    },
    beforeDestroy() {
  
    },
    destroyed() {
  
    }
}
</script>

