          input{
            -webkit-appearance: none;
            -moz-appearance: none;
            -ms-appearance: none;
            width: 80px;
            height: 2px;
            background-color: #bbbbbb;
            position: absolute;
            left: 0;

            &::-webkit-slider-thumb{
              -webkit-appearance: none;
            }
            &::-moz-range-trackpseduo{
              -moz-appearance: none;
            }
            &::-ms-track {
              width: 100%;
              cursor: pointer;
              background: transparent; /* Hides the slider so custom styles can be added */
              border-color: transparent; color: transparent;
            }
            &:focus{
              outline: none;
            }
            &::-webkit-slider-thumb{
                -webkit-appearance: none;
                height: 9px;
                width: 9px;
                margin-top: -1px;
                background: #bbb;
                border-radius: 50%;
