                height: 6px;
                width: 6px;
                margin-top: -1px;
                background: #bbb;
                border-radius: 50%;
                border: solid 0.125em rgba(205, 224, 230, 0.5);
            }
            &::-ms-track{
                -moz-appearance: none;
                height: 6px;
                width: 6px;
                margin-top: -1px;
                background: #bbb;
                border-radius: 50%;
                border: solid 0.125em rgba(205, 224, 230, 0.5);
