export function getStatistics(duration_num, type_num) {
    return axios({
        url: 'api/sidb/stat/'+ duration_num + '/'+ type_num,
        method: 'get',
        data: ''
    })
}


/* 检索详情 -- 进入页面即执行 */
export function fetchDetail(param) {
    return axios({
        url: 'admin/speakers/' + param, // 0605  spk_records 改为 speaker
        method: 'get',
        data: '',
    })
}

