/* 说话人检索 -- 进入页面即执行, get */
export function fetchData(page_start, page_size) {
    return axios({
        // url: 'admin/retrieval_task',
        url: 'admin/tasks/'+ page_start + '/' + page_size , /* 0605 */
        method: 'get',
        data: '',
    })
}

/* 说话人检索 -- 附带检索选项, post */
export function getData(query) {
  return axios({
    // url: '/api/objects',
    // url: 'admin/retrieval_task',
    url: 'admin/tasks', /* 0528 */
    method: 'post',
    data: query,
    headers: {'Content-Type': 'multipart/form-data'}
  })
}

export function login(query) {
  return axios({
    url: '/admin/login',
    method: 'post',
    data: query
  })
}


