        /* 0524 选择文件 */
        getFile() {
            var upfile = document.getElementById('upfile');
            upfile.click();

            var f = document.getElementById('fileName');
            console.log(upfile.files[0]); // object类型
            this.voiceFile = upfile.files[0]
            if(upfile.files[0]) f.innerHTML = upfile.files[0].name;

            // let FileList = Array.prototype.slice.call(this.$refs.resource.files);
            // console.log('ref_type: %o \nref_音频: %o', typeof FileList, FileList)
        },
        /* 0523 点击不同tab隐藏或显示部分搜索条件*/
        changeTabAndCond(e){
            // alert(e);
            let el = e.currentTarget;
            let delAtVoice = document.querySelectorAll('.voice');
            let noQuery = document.querySelector('.management .right .lbtn');
            let voQuery = document.querySelector('.management .right .rbtn');
            if (el.innerText == '语音检索') {
                // console.error('AAA[this]: ',this);
                this.resultValue = '';
                this.phoneNumber = '';
                this.languageValue = '';
                this.genderValue = '';
                this.cityValue = '';
                this.speakerName = '';

                /* 0604 赋一个点击标签页名的属性,方便后面号码/语音检索跳转时判断用 */
                this.queryTabSelected = el.innerText;

                /* 增加两项搜索条件 */
                noQuery.setAttribute('style', 'color: #000 !important; background-color: #fff !important');
                voQuery.setAttribute('style', 'color: #fff !important; background-color: #6e8de3 !important');
                for (let i=0; i<delAtVoice.length; i++) {
                    delAtVoice[i].classList.remove('hidden')
                }
                /* 隐藏号码 */
                document.querySelector('.phoneNo').classList.add('hidden');

            } else {
                /* 0604 赋一个点击标签页名的属性,方便后面判断"号码/语音检索"跳转时用 */
                this.queryTabSelected = el.innerText;

                /* 清空上传文件 0524 */
                // var upfile = document.getElementById('upfile');
                // if (upfile.outerHTML) {
                //     upfile.outerHTML = upfile.outerHTML;
                // } else { // for FF
                //     upfile.value = "";
                // }
                document.getElementsByTagName('form')[0].reset()
                var fN = document.getElementById('fileName');
                fN.innerHTML = "<span style='color:#ccc'>请选择语音文件</span>"; /* 还原模拟的placeholder */
                this.voiceFile = '';
                this.resultValue = '';
                this.languageValue = '';
                this.genderValue = '';
                this.cityValue = '';
                this.speakerName = '';
                this.threshold = '';

                noQuery.setAttribute('style', 'color: #fff !important; background-color: #6e8de3 !important');
                voQuery.setAttribute('style', 'color: #000 !important; background-color: #fff !important');
                for (let i=0; i<delAtVoice.length; i++) {
                    // console.log('%cdelAtVoice[i]','color:brown',delAtVoice[i])
                    delAtVoice[i].classList.add('hidden')
                }
                /* 显示号码 */
                document.querySelector('.phoneNo').classList.remove('hidden');
            }
            // console.warn('当前项：',el.innerText)
        },
