        cellClickedHandle (e) {
            // alert(e)
            // console.log('%ccell_Click_e:','color:purple;font:bold',e)
            // console.log('%ccell_Click_this:','color:blue;font;bold',this)
            
            /* 通过e.value来判别点击了哪个cell,仅当点击结果列非0数量的格子时展开侧滑面板 */
            if(e.value.indexOf('查看') != -1) {
                // fetchDetail(e.data.id).then(res=>{
                //     console.log('%ccellClickedHandle @ fetchDetail.then()_res.data:','color:orange',res.data)
                //     this.resdata = res.data
                // })

                /* 下方功能保留,'语音检索'时会在此页面点击'查看'跳转; '号码检索'时直接跳转到详情页 */
                /* 将检索页选中的那行数据的任务id传给详情页,而不是将那行数据背后的数据取得后再整个传给详情页 */
                this.$router.push({path: '/myApplication/voiceprint/detail', query: {taskId: e.data.id}});
            }
        },
        searchBtn () {
            // var formData = new FormData();
            // let FileList = Array.prototype.slice.call(this.$refs.resource.files);
            // FileList.map((file, index) => {
            //       formData.append("filedata", file);
            //     }); /* 0524 @汤 */

            /* ---- 传参部分 ---- */
            let params = new FormData()
            params.append('PhoneNum',this.phoneNumber)
            params.append('Lang',this.languageValue)
            params.append('Gender',this.genderValue)
            // params.append('City',this.cityValue) // 居然就是数字代码| 暂时未明哪里做的变换 | 用下拉框时,参数这么传
            params.append('City', this.antiCityCode[this.citySelectVal] ? this.antiCityCode[this.citySelectVal] : '')
            params.append('MaxCount',this.resultValue)
            params.append('Speaker',this.speakerName) // 0522 add
            params.append('VoiceFile', this.voiceFile ) //0524 add

            /* ---- 0525 页面选项填入的部分, @陈 拿出一部分先处理了一小块儿的显示 ---- */
            // console.error('this:',this) // 本次搜索创建的实例
            // let obj = {}
            // obj.number = this.index++
            // this.languageValue == ''?obj.language = '不限':obj.language = this.languageValue
            // obj.gender = this.antiGender[this.genderValue]
            // obj.startTime = new Date().format("yyyy-MM-dd hh:mm:ss")
            // obj.status = '检索中'
            // this.cityValue == ''?obj.city = '不限':obj.city = this.cityCode[this.cityValue]
            //this.rowData.unshift(obj)   // 0525 雷表示要倒序显示最新添加的检索,
                                          //      rowData(i.e. getData里let声明的data,亦即我逆序过的数组)
                                          //      会直接作用于页面显示

            if (this.queryTabSelected == '号码检索') {
                // if (!this.phoneNumber) {
                //     this.$message({
    	        //   	    message: '请先输入检索号码！',
                //         type: 'warn',
                //         duration: 1200,
                //     })
                //     return;
                // }
                // if (!/^[0-9]*$/.exec(this.phoneNumber)) {
                //     this.$message({
                //         message: '检索号码仅可使用数字！',
                //         type: 'warn',
                //         duration: 1200,
                //     })
                //     return;
                // }

                /* 表格呈现内容之更新 -- 号码 */
                let obj_cur = {}; // 当前页面的数据作为obj_cur
                obj_cur.number = 1; // current is always 1st
                obj_cur.startTime = new Date().format("yyyy-MM-dd hh:mm:ss"); // then中的与此不同,要覆盖
                obj_cur.status = "检索中";
                obj_cur.language = (this.languageValue=='')?'不限':this.languageValue;
                obj_cur.gender = (this.genderValue==1)?'男':((this.genderValue==2)?'女':'不限');
                obj_cur.speaker = !this.speakerName?'未知':this.speakerName;

                this.rowData.unshift(obj_cur);
                // console.log('%c最初的加入搜索对象的rowData__号码检索:','color:green',this.rowData);
                this.rowData.splice(10);
                // console.log('%cthis.rowData_after_splice()__号码检索:','font-weight:700;font-style:italic',this.rowData);
                /* 更新rowData中后面2~9个数据的number */
                for (let i = 1; i < (this.rowData.length < 10 ? this.rowData.length : 10); i++) {
                    // console.warn('>>>',i)
                    // console.log('>>>',this.rowData[i])
                    this.$set(this.rowData[i], 'number', i+1);
                    // console.log('<<<',this.rowData[i])
                }

                getData(params).then(res=>{
                    // console.log('%c创建<号码>检索后_this:','font-weight:700',this)
                    // console.log('%cin_getData.then()_res.data:','color:orange',res.data) // 搜索创建的单条数据;object类型
                    this.resdata = res.data;
                    let resp = res.data.Data;
                    
                    if (resp.Total) { // 号码检索结果为0时不跳转; 非0时才跳转
                        // console.warn('非0时,obj_cur:',obj_cur)

                        obj_cur.startTime = new Date(resp.CreateTime*1000).format("yyyy-MM-dd hh:mm:ss");
                        obj_cur.status = (resp.State==true)?'成功':'失败';
                        obj_cur.threshold = (resp.Threshold==undefined)?'未知':resp.Threshold;
                        obj_cur.id = resp.TaskId;
                        obj_cur.city = !resp.City?'不限':this.cityCode[resp.City];
                        obj_cur.detail = '查看('+ resp.Total +')'; // if里已判明此分支里的Total非0
                        
                        // console.warn('非0时,obj_cur更新后:',obj_cur)
                        // console.log('%cthis.rowData_in_then()_不为0:','color:aqua',this.rowData);

                        this.$set(this.rowData[0], this.rowData[0], obj_cur); // 更新this.rowData中第一个数据, 即补完表格第一行数据
                        this.RES_DATALIST(this.rowData) // 仅作参照, 无甚大用

                        this.$router.push({path: '/myApplication/voiceprint/detail', query: {taskId: res.data.Data.TaskId}})
                    } else { // 号码检索结果为0时, 不跳转
                        // console.warn('为0时,obj_cur:',obj_cur)

                        obj_cur.startTime = new Date(resp.CreateTime*1000).format("yyyy-MM-dd hh:mm:ss");
                        obj_cur.status = (resp.State==true)?'成功':'失败';
                        obj_cur.threshold = (resp.Threshold==undefined)?'未知':resp.Threshold;
                        obj_cur.id = resp.TaskId;
                        obj_cur.city = !resp.City?'不限':this.cityCode[resp.City];
                        obj_cur.detail = '0';

                        // console.warn('为0时,obj_cur更新后:',obj_cur)
                        // console.log('%cthis.rowData_in_then()_为0:','color:purple',this.rowData);

                        this.$set(this.rowData[0], this.rowData[0], obj_cur);
                        this.RES_DATALIST(this.rowData)

                    }
                });
            }
            if (this.queryTabSelected == '语音检索') {
                if (!this.voiceFile) {
                    this.$message({
    	          	    message: '请先选择检索文件！',
                        type: 'warn',
                        duration: 1200,
                    })
                    return;
                }
                
                /* 语音检索时, 增加 Modal 流程 */
                this.showModal();
                // return;

                /* 表格呈现内容之更新 -- 语音 */
                let obj_cur = {}; // 当前页面的数据作为obj_cur
                obj_cur.number = 1; // current is always 1st
                obj_cur.startTime = new Date().format("yyyy-MM-dd hh:mm:ss"); // then中的与此不同,要覆盖
                obj_cur.status = "检索中";
                obj_cur.language = (this.languageValue=='')?'不限':this.languageValue;
                obj_cur.gender = (this.genderValue==1)?'男':((this.genderValue==2)?'女':'不限');
                obj_cur.speaker = !this.speakerName?'未知':this.speakerName;
                
                this.rowData.unshift(obj_cur);
                // console.log('%c最初的加入搜索对象的rowData__号码检索:','color:green',this.rowData);
                this.rowData.splice(10);
                // console.log('%cthis.rowData_after_splice()__语音检索:','font-weight:700;font-style:italic',this.rowData);
                
                /* 更新rowData中后面2~9个数据的number */
                for (let i = 1; i < (this.rowData.length < 10 ? this.rowData.length : 10); i++) {
                    // console.warn('>>>',i)
                    // console.log('>>>',this.rowData[i])
                    this.$set(this.rowData[i], 'number', i+1);
                }

                getData(params).then(res=>{
                    // console.log('%c创建<语音>检索后_this:','font-weight:700',this)
                    // console.log('%cin_getData.then()_res.data:','color:orange',res.data) // 搜索创建的单条数据;object类型
                    this.resdata = res.data
                    let resp = res.data.Data;

                    if (resp.Total) {                                          // 后台处理后,返回的检索结果若不为0,则自动跳转
                        this.closeModal();

                        obj_cur.startTime = new Date(resp.CreateTime*1000).format("yyyy-MM-dd hh:mm:ss");
                        obj_cur.status = (resp.State==true)?'成功':'失败';
                        obj_cur.threshold = (resp.Threshold==undefined)?'未知':resp.Threshold;
                        obj_cur.id = resp.TaskId;
                        obj_cur.city = !resp.City?'不限':this.cityCode[resp.City];
                        obj_cur.detail = '查看('+ resp.Total +')';             // if里已判明此分支里的Total非0

                        this.$set(this.rowData[0], this.rowData[0], obj_cur);  // 更新this.rowData中第一个数据, 即补完表格第一行数据
                        this.RES_DATALIST(this.rowData)                        // 仅作参照, 无甚大用

                        this.$router.push({path: '/myApplication/voiceprint/detail', query: {taskId: res.data.Data.TaskId}})
                    } else {                                                   // 语音检索结果为0时, 留在本页
                        this.closeModal();

                        obj_cur.startTime = new Date(resp.CreateTime*1000).format("yyyy-MM-dd hh:mm:ss");
                        obj_cur.status = (resp.State==true)?'成功':'失败';
                        obj_cur.threshold = (resp.Threshold==undefined)?'未知':resp.Threshold;
                        obj_cur.id = resp.TaskId;
                        obj_cur.city = !resp.City?'不限':this.cityCode[resp.City];
                        obj_cur.detail = '0';

                        // console.warn('为0时,obj_cur更新后__语音检索:',obj_cur)
                        // console.log('%cthis.rowData_in_then()_为0__语音检索:','color:purple',this.rowData);

                        this.$set(this.rowData[0], this.rowData[0], obj_cur);
                        this.RES_DATALIST(this.rowData)

                    }                   
                });
            }
        },
        handleCurrentChange(val) {
            // this.pageData = this.rowData.slice(this.pageNum*(val-1),this.pageNum*val)
            // alert(val)
            this.currentPage = val;
            // console.log(`当前页: ${val}`);
            // console.log('%cthis @ handleCurrentChange:', this); // 空
            fetchData(this.currentPage-1, this.pageNum).then(res=>{
                // console.log('%cmounted_fetchData.then()_res.data__# handleCurrentChange:','color:orange',res.data)

                this.resdata = res.data
                this.totalCounts = this.resdata.Count;

                // let data = this.getresdataList()
                let data = []; //  每次都请求后台,所以每次都清空页面上的数据
                this.rowData = [];
                data.push(res.data)

                // this.RES_DATA(res.data)
                this.RES_DATALIST(data)

                var dataArray = this.resdata.Data;
                // dataArray.reverse(); // 后台逆序输出,我就不必再反向处理了
                for (let i=0; i<dataArray.length; i++) {
                    let obj = {};
                    obj.number = i+1;
                    obj.startTime = new Date(dataArray[i].CreateTime*1000).format("yyyy-MM-dd hh:mm:ss");
                    obj.language = (dataArray[i].Lang=='')?'不限':dataArray[i].Lang;
                    obj.city = (dataArray[i].City=='')?'不限':this.cityCode[dataArray[i].City];
                    obj.gender = (dataArray[i].Gender==1)?'男':((dataArray[i].Gender==2)?'女':'不限');
                    obj.threshold = (dataArray[i].Threshold == 0)?'':dataArray[i].Threshold;
                    obj.id = dataArray[i].TaskId;
                    obj.speaker = (dataArray[i].SpeakerName=='')?'未知':dataArray[i].SpeakerName;
                    obj.status = (dataArray[i].State==true)?'成功':'失败';
                    obj.detail = (dataArray[i].Total==0)?'0':'查看('+ dataArray[i].Total +')';
                    // console.log('obj:',obj)
                    this.rowData.push(obj)
                    this.$set(this.rowData[i],this.rowData[i],obj)
                }
            })

        },
        handleSizeChange(val) {
            this.pageNum = val;
            // alert(val)
            // console.log(`当前每页数量: ${val}`);
            // console.log('%cthis @ handleSizeChange:', this);

            fetchData(this.currentPage-1, this.pageNum).then(res=>{
                // console.log('%cmounted_fetchData.then()_res.data__# handleSizeChange:','color:purple',res.data)

                this.resdata = res.data
                this.totalCounts = this.resdata.Count;

                // let data = this.getresdataList()
                let data = [];
                this.rowData = [];
                // console.log('%c *** data ***__# handleSizeChange:','color:green;font-weight:700',data)
                data.push(res.data)

                this.RES_DATALIST(data)

                var dataArray = this.resdata.Data;
                // dataArray.reverse(); // 后台逆序输出,我就不必再反向处理了
                for (let i=0; i<dataArray.length; i++) {
                    let obj = {};
                    obj.number = i+1;
                    obj.startTime = new Date(dataArray[i].CreateTime*1000).format("yyyy-MM-dd hh:mm:ss");
                    obj.language = (dataArray[i].Lang=='')?'不限':dataArray[i].Lang;
                    obj.city = (dataArray[i].City=='')?'不限':this.cityCode[dataArray[i].City];
                    obj.gender = (dataArray[i].Gender==1)?'男':((dataArray[i].Gender==2)?'女':'不限');
                    obj.threshold = (dataArray[i].Threshold == 0)?'':dataArray[i].Threshold;
                    obj.id = dataArray[i].TaskId;
                    obj.speaker = (dataArray[i].SpeakerName=='')?'未知':dataArray[i].SpeakerName;
                    obj.status = (dataArray[i].State==true)?'成功':'失败';
                    obj.detail = (dataArray[i].Total==0)?'0':'查看('+ dataArray[i].Total +')';
                    // console.log('obj:',obj)
                    this.rowData.push(obj)
                    this.$set(this.rowData[i],this.rowData[i],obj)
                }
            })
        },
    },
    beforeCreate() {
    },
    created() {
        let antiCityCode = {}
        for(let k in cityCode){
            antiCityCode[cityCode[k]] = k
        }
        this.cityCode = cityCode
        this.antiCityCode = antiCityCode
        // console.log('%cthis.cityCode: %o\nthis.antiCityCode: %o','color:pink',this.cityCode,this.antiCityCode)
        // console.error('test_cityCode:', this.cityCode['010'])
        
        // console.log('%cthis.$route: ','color:blue',this.$route)
        // console.log('%cthis: ','color:blue',this)
        // console.log('%cquery -- Created()','color:red')
    },
    beforeMount() {
    },
    mounted() {
    // console.log(this.cityCode)
    // console.log(this.antiCityCode)
        /* 城市自动选择功能 */
        this.cities = this.loadAll();

        utils.dateFormat()
        this.gridOptions.api.sizeColumnsToFit()
        // window.addEventListener('resize', () => {
        //     this.gridOptions.api.sizeColumnsToFit()
        // })
        
        /* 进入页面立即载入已有的检索任务 0524 */
        /* 检索任务api变更, 载入时赋予初始默认值, 初始检索页:0,每页展示this.pageNum条数据 */
        fetchData(this.currentPage-1, this.pageNum).then(res=>{
            // console.log('%cmounted_fetchData.then()_res.data:','color:orange',res.data)

            this.resdata = res.data
            this.totalCounts = this.resdata.Count; // 拿到总数据的数目

            this.RES_DATALIST(res.data.Data) // 让$store.state.resdataList属性载入数据
            // console.log('%cthis.$store:','color:aqua',this.$store)
            console.log('%cthis:','color:aqua',this)

            // console.log(new Date(1527218174000).format("yyyy-MM-dd hh:mm:ss"))
            // console.log(utils.dateFormat(test,format("yyyy-MM-dd hh:mm:ss"))) //失败
            var dataArray = this.resdata.Data;
            // dataArray.reverse();
            // console.log('%cdataArray.reversed:','color:purple',dataArray)
            for (let i=0; i<dataArray.length; i++) {
                let obj = {};
                obj.number = i+1;
                obj.startTime = new Date(dataArray[i].CreateTime*1000).format("yyyy-MM-dd hh:mm:ss");
                obj.language = (dataArray[i].Lang=='')?'不限':dataArray[i].Lang;
                obj.city = (dataArray[i].City=='')?'不限':this.cityCode[dataArray[i].City];
                obj.gender = (dataArray[i].Gender==1)?'男':((dataArray[i].Gender==2)?'女':'不限');
                obj.threshold = (dataArray[i].Threshold == 0)?'':dataArray[i].Threshold;
                obj.id = dataArray[i].TaskId;
                obj.speaker = (dataArray[i].SpeakerName=='')?'未知':dataArray[i].SpeakerName;
                obj.status = (dataArray[i].State==true)?'成功':'失败';
                obj.detail = (dataArray[i].Total==0)?'0':'查看('+ dataArray[i].Total +')';
                // console.log('obj:',obj)
                this.rowData.push(obj)
                // this.$set(this.rowData[i],this.rowData[i],obj) // 刚加载时哪有必要响应式控制数据
            }
            // console.error('this.rowData_mounted():', this.rowData) // __ob__: Observer
        })
        
    },
    beforeUpdate() {
    },
    updated() {
        // console.warn('this @ updated:', this)
        // console.warn('this.antiCityCode[this.citySelectVal] @ updated:',this.antiCityCode[this.citySelectVal])
        // if (this.timeVal) alert(this.timeVal)
        var child = document.querySelector('.el-icon-date');
        if(child) child.parentNode.removeChild(child);

        window.addEventListener('resize', () => {
            this.gridOptions.api.sizeColumnsToFit()
        })
    },
    activated() {
        var table_content_height = document.querySelector('.table_content');
        this.table_content_height = table_content_height.offsetHeight + 'px';
        // alert('query.vue: ' + this.table_content_height)
    },
    deactivated() {
    },
    beforeDestroy() {
    },
    destroyed() {
    }
}
</script>

