/* 定义store中的state里要存哪些字段,然后就能在getter.js中通过这些属性供外部调用
        ---- state用于存储数据，类似vue实例的data属性 ----
 */
const state = {
	account: '',
	resdata:{},
	resdataList:[],
	listIndex:'',
    audioPath:'',
	audioPathList:{
		samplePaths:[],
		clusterPaths:[],
		trainPaths:[],
		dateFlag:'',
	},
	taskId:'',
	qlData:{
		caller:'',
		callee:'',
		starttime:'',
		opname:'',
		returnTo:''
	},
	importTaskId: '',
	auth:{
		sys_userManagement: null,  	//1  用户管理
		search_defraud: null,       //21 诈骗
		search_condition: null,			//8  检索
		mon_list: null,							//20 实时预警
		search_importVoice: null,		//11 导入语音
		mycase_man: null,						//3  案件管理
		res_keywords: null,       	//5  关键词
		res_speakers: null,      		//6  声纹
		res_speechseg: null,     		//4  固定音频
		aaaaaaa: null,       				//12  状态监测
		bbbbbbb: null,       				//14  搜索队列
	},
	phoneNumbers:[],
	sectionTree:[],
	speakerId:'',            //上传样本 声纹id
	speakerFilename: '',     //上传样本 文件名
	refresh: '',            //更新样本视图 存时间戳
	caseData: {
		keywords: [],
		speechsegs: [],
		telrules: [],
		id: ''
	},
	platform:{
		platform: '',
        platformOptions: [],
    },
    origin_voice: [], // 0620 @汤 以下2个属性, 说话人管理会用到
	train_voice: [],
}
export default state


