<template>
  <div class="charts">
    <div class="search">
      <ul>
        <li class="lang">
          <span>语种</span>
          <el-select 
            v-model="languageValue" 
            placeholder="请选择"
            multiple
            collapse-tags
            style="margin-left: 20px;">
            <el-option
              v-for="item in language"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </li>
        <li class="timeSelect">
          <span>时间</span>
          <el-date-picker
            :editable=false
            v-model="timrPicker"
            type="datetimerange"
            :picker-options="pickerOptions"
            range-separator="至"
            start-placeholder="开始"
            end-placeholder="结束"
            align="left"
            @change="timeChange">
          </el-date-picker>
        </li>
        <li>
          <span>性别</span>
          <el-select v-model="languageValue1" placeholder="请选择">
            <el-option
              v-for="item in language1"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </li>
        <li>
          <span>城市</span>
          <el-select v-model="languageValue1" placeholder="请选择">
            <el-option
              v-for="item in language1"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </li>
        <li>
          <el-button type="primary" class="myBtn">开始检索</el-button>
        </li>
      </ul>
    </div>
    <line-chart></line-chart>
    <div class="group">
      <bar-chart class="barChart"></bar-chart>
      <pie-chart class="pieChart"></pie-chart> 
    </div>
    
  </div>
</template>

<script>
import echarts from 'echarts'
import lineChart from './charts/lineChart.vue'
import barChart from './charts/barChart.vue'
import pieChart from './charts/pieChart.vue'
export default {
  name: 'charts',
  data () {
    return {
      phoneNumber: '123456',
      languageValue: '汉语',
      language:[
        {
          value: '汉语',
          label: '汉语'
        },
        {
          value: '维语',
          label: '维语'
        },
        {
          value: '沪语',
          label: '沪语'
        },
        {
          value: '英语',
          label: '英语'
        },
        {
          value: '日语',
          label: '日语'
        }
      ],
      languageValue1: '汉语',
      language1:[
        {
          value: '汉语',
          label: '汉语'
        },
        {
          value: '维语',
          label: '维语'
        },
        {
          value: '沪语',
          label: '沪语'
        },
        {
          value: '英语',
          label: '英语'
        },
        {
          value: '日语',
          label: '日语'
        }
      ],
      pickerOptions: {
        shortcuts: [{
          text: '最近一天',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 1);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一周',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近三个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      timrPicker: [],
      timeSelectOptions: [
        {
          value:'最近一天',
          label:'最近一天'
        },{
          value:'最近三天',
          label:'最近三天'
        },{
          value:'最近一周',
          label:'最近一周'
        },{
          value:'最近一个月',
          label:'最近一个月'
        }
      ],
      timeSelect: '',

    }
  },
  components: {
    'line-chart': lineChart,
    'bar-chart': barChart,
    'pie-chart': pieChart
  },
  methods:{
    timeChange() {

    }
  },
  beforeCreate() {

  },
  created() {

  },
  beforeMount() {

  },
  mounted() {

  },
  beforeUpdate() {

  },
  updated() {

  },
  activated() {

  },
  deactivated() {

  },
  beforeDestroy() {

  },
  destroyed() {

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  .charts{
    width: 100%;
    // height: 100%;
    display: flex;
    flex-direction: column;

    .search{
      width: 100%;
      height: 130px;
      margin-bottom: 20px;
      // box-shadow: 0px 0px 10px #888888;
      border-radius: 5px;
      background-color: #fff;
      
      position: relative;
      padding: 33px 30px 0 30px;
      ul{
        display: flex;
        // height: 130px;
        li{
          display: flex;
          flex-direction: column;
          margin-right: 30px;
          span{
            font-size: 14px;
            color: #999;
            margin-bottom: 15px;
          }

          &:nth-last-child(1){
            position: absolute;
            right: 30px;
            bottom: 35px;
            
          }
        }
      }
    }
    .group{
      width: 100%;
      display: flex;
    }
  }
  
</style>


