<template>
  <div class="login"  @keyup.13="jumpTo('/myApplication/voiceprint/increment')" ><!-- 走的是路由的路径,而非实际文件名路径 0522-0625 -->
    <div class="bg"></div>
	  <div class="login_form">
	  	<div class="title">优浪智能全民声纹库</div>
      <div class="content">
        <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" class="demo-ruleForm">
          <el-form-item prop="account">
            <el-input
              type=""
              v-model="ruleForm2.account"
              auto-complete="off"
              placeholder="请输入账号"
              prefix-icon="accountIcon"
            >       
            </el-input>
          </el-form-item>
          <el-form-item prop="pass">
            <el-input
              type="password"
              v-model="ruleForm2.pass"
              auto-complete="off"
              placeholder="请输入密码"
              prefix-icon="passwordIcon"
            >   
            </el-input>
          </el-form-item>
          <el-checkbox class="rememberPassword" @change="checked" v-model="rememberPassword">记住密码</el-checkbox>
          <el-button @click="jumpTo('/myApplication/voiceprint/increment')" type="primary">登&nbsp;&nbsp;&nbsp;录</el-button> <!-- 0522 改成下行 -->
          <!-- <el-button @click="jumpTo('/voiceprint/query')" type="primary">登&nbsp;&nbsp;&nbsp;录</el-button> -->
          <!-- <el-button @click="jumpTo()" type="primary">登&nbsp;&nbsp;&nbsp;录</el-button> -->
        </el-form>
      </div>	
	  </div>
    <div class="copyright">
      <ul>
        <li>关于优浪</li>
        <li>服务协议</li>
        <li>运维中心</li>
        <li>联系邮箱</li>
      </ul>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import {login} from '@/api/index.js'
import {mapActions,mapGetters} from 'vuex'
export default {
  name: 'login',
  data () {
  	var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'));
      } else {
        this.ruleForm2.pass = value
        callback();
      }
    };
    var validateAccount = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入账号'));
      } else {
        this.ruleForm2.account = value
        callback();
      }
    };
    return {
    	ruleForm2: {
        pass: '',
        account: ''
      },
      rules2: {
        pass: [
          { validator: validatePass, trigger: 'blur' }
        ],
        account: [
          { validator: validateAccount, trigger: 'blur' }
        ]
      },
      rememberPassword: false
      
    }
  },
  methods: {
    // jumpTo (src) {
    //   this.$router.push({path: src})
    // }
    ...mapActions(['ACCOUNT']),
    checked(){
      console.log('%cthis.rememberPassword@login.Vue','color:green',this.rememberPassword)
    },
  	jumpTo (src) {
      // var date = new Date()
      // var maxAge = date.getTime() + 60*60*24*30*1000
      // console.log('date.getTime()')
      // console.log(date.getTime())
      // console.log(maxAge)
      var params = new URLSearchParams()
      params.append('Username',this.ruleForm2.account)
      params.append('Password',this.ruleForm2.pass)

      // document.cookie = `username=${this.ruleForm2.account};Max-Age=2592000`
      // document.cookie = `remember=${this.rememberPassword};Max-Age=2592000`
      // console.log(login())
      
      login(params).then(res=>{
        console.log('%cres.data # login:', res.data)
        if(!res.data.Code){
          this.$router.push({path: src})
          this.ACCOUNT(this.ruleForm2.account)
          if(this.rememberPassword){
            document.cookie = `password=${this.ruleForm2.pass};Max-Age=2592000`
            
          }
          console.log(this.$store)
        }else{
          this.$message.error('用户名或密码错误');
          console.log('%cErrorWhenCall_login()','color:green',res)
        }
      }).then(() => {
        document.cookie = `username=${this.ruleForm2.account}`
        document.cookie = `remember=${this.rememberPassword}`
      })
      // .catch((error)=>{
      //   console.log(error)
      // })
    
    // axios({
    //     withCredentials: true,
    //     method: 'post',
    //     url: `http://192.168.1.95:8080/webvoice/user/login.do`,
    //     data: params
    //   }).then(res =>{
    //     console.log(res)
    //     console.log(document.cookie)
    //   }).then(()=>{
    //     var params = new URLSearchParams()
    //     params.append('start','1')
    //     params.append('limit',25)

    //     // document.cookie="username=admin;path=/;domain=192.168.1.92";
    //     // document.cookie="remember=false;path=/;domain=192.168.1.92";
    //     // document.cookie="autologin=false;path=/;domain=192.168.1.92";
    //     axios({
    //       withCredentials: true,
    //       method: 'get',
    //       url: `http://192.168.1.95:8080/webvoice/keyword/find.do?_dc=1519958179368&page=1&start=0&limit=25`,
    //       // data: params
    //     }).then(res =>{
    //       console.log(res)
    //       console.log(document.cookie)
    //     })
    //   })
  	},
    parseCookie() {
      var cookie = document.cookie,
      pattern = /([^=]+)=([^;]+);?\s*/g,
      result,
      value = {};
      while((result = pattern.exec(cookie)) != null) {
        value[result[1]] = result[2];
        console.log(result)
      }
      return value;
    }
  },

  created() {


    if(this.parseCookie()){
      //username=admin; remember=true; password=123456

      let arr = this.parseCookie()
      this.ruleForm2.account = arr['username']
      arr['remember'] === "false" ? this.rememberPassword = false : this.rememberPassword = true

      if(arr['remember'] === "true"){

        this.ruleForm2.pass = arr['password']
        
      }else{
        this.ruleForm2.pass = ''
      }
      
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
  ul,li{
    list-style: none;
  }
	.login{
    .el-form-item__content{
      border: none;
    }
		.el-input,.el-button{
			width: 254px;
			height: 40px;
      
		}
    .accountIcon::before{
      content: '';
      height: 16px;
      width: 16px;
      display: block;
      background: url('../../static/images/account.png');
      margin-top: 12px;
    }
    .passwordIcon::before{
      content: '';
      height: 16px;
      width: 16px;
      display: block;
      background: url('../../static/images/password.png');
      margin-top: 12px;
    }
	}
</style>
<style scoped lang="scss">
  @media (max-width: 1366px){
    .bg{
      margin-left: 100px !important;
    }
    .login_form{
      margin-right: 100px !important;
    }
  }
	.login{
		height: 100%;
		display: flex;
  	align-items: center;
    // flex-wrap: wrap;
    // background-color: #3071b8;
    background: url('../../static/images/loginbg.jpg');
    -webkit-background-size: 100% 100%;
    -moz-background-size: 100% 100%;
    background-size: 100% 100%;
    
    position: relative;
    .bg{
      width: 709px;
      height: 512px;
      // margin-left: 400px;
      margin-left: 300px;
      background: url('../../static/images/login.png');
    }
		.login_form{
			width: 340px;
      // margin-left: 600px;
			margin-left: auto;
      margin-right: 300px;
      .rememberPassword{
        margin-bottom: 22px;
      }
			.title{
				height: 45px;
				line-height: 45px;
				text-align: center;
				background-color: #2e3233;
				color: #fff;
				// margin-bottom: 20px;
				border-radius: 5px 5px 0 0;
			}
      .content{
        // height: 315px;
        padding: 13px;
        background: rgba(255, 255, 255, .2);
        border-radius: 0 0 5px 5px;
        .el-form{

          background-color: #fff;
          border-radius: 3px;
          height: 315px;
          padding: 30px;
          padding-top: 50px;
        }
      }
		}
    .copyright{
      position: absolute;
      bottom: 60px;
      width: 100%;
      // text-align: center;
      margin: 0 auto;
      ul{
        display: flex;
        color: #4aa2da;
        justify-content: center;
        li{
          margin: 0 10px 0 10px;
          position: relative;
          &:after{
            content: '';
            width: 1px;
            height: 18px;
            background-color: #4aa2da;
            display: block;
            position: absolute;
            left: -10px;
            top: 2px;
          }
          &:nth-child(1)::after{
            display: none;
          }
        }
      }
    }
	}
</style>



