<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
    .management .el-button {
        width: 120px;
        height: 36px;
        padding: 6px 10px;
        border-radius: 20px;
    }
    .btn-exp {
        background-color:#6e8de3;
        border-color: #6e8de3;
        color: #fff;
    }
    .table .table_head .left {
        margin-left: 0
    }
    .pos-r {
        position: relative;
    }
    .refresh {
        position: absolute;
        top: 8px;
        left: 210PX;
    }
    
    .hidden {
        display: none !important
    }
    
    input[type=number]::-webkit-inner-spin-button,  
    input[type=number]::-webkit-outer-spin-button {  
        -webkit-appearance: none;
    }

</style>

<style scoped lang="scss">
    .charts{
        width: 100%;
        // height: 100%;
        display: flex;
        flex-direction: column;

        .search{
            width: 100%;
            height: 130px;
            margin-bottom: 20px;
            // box-shadow: 0px 0px 10px #888888;
            border-radius: 5px;
            background-color: #fff;
      
            position: relative;
            padding: 33px 30px 0 30px;
            ul{
                display: flex;
                // height: 130px;
                li{
                    display: flex;
                    flex-direction: column;
                    margin-right: 30px;
                    span{
                        font-size: 14px;
                        color: #999;
                        margin-bottom: 15px;
                    }

                    &:nth-last-child(1){
                        position: absolute;
                        right: 30px;
                        bottom: 35px;
                    }
                }
            }
        }
        .group{
            width: 100%;
            display: flex;
        }
    }

</style>


