<template>
  <div class="barChart"></div>
</template>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  .barChart{
    width: calc(50% - 10px);
    height: 400px;
    margin-right: 20px;
    // box-shadow: 0px 0px 10px #888888;
    border-radius: 5px;
    background-color: #fff !important;
  }
</style>

<script>
import echarts from 'echarts'
import {getStatistics} from '@/api/index.js'

export default {
  name: 'barChart',
  data () {
    return {
      chart: null,
      statisticArr: [],
    }
  },
  props: ['barChartDatenum'],
  watch: {
      barChartDatenum(newVal, oldVal) {
        //   console.log(newVal);
        //   this.initChart();
        this.statisticArr = [];
        getStatistics( this.barChartDatenum, 1 ).then(res=>{
            // console.warn('~~~~~res.data.Data(语种)__在watch()中~~~~~~:', res.data.Data)
            for (var i = 0; i < 7; i++) {
                this.statisticArr.push(res.data.msg[i].Count)
            }
            this.initChart()
        });

      }
  },
  methods:{
    initChart() {
      this.chart = echarts.init(document.querySelector('.barChart'))
      this.chart.group = 'brick';
    //   console.log('%cthis.chart:','color:purple', this.chart)

      this.chart.setOption({
        title: {
          text: '语种分布图',
          top: 10,
          left: 10,
          textStyle:{
            fontSize: 16
          }
        },
        tooltip: {
          trigger: 'axis'
        },
        xAxis: {
          type: 'category',
          data: ['英语','日语','韩语','汉语','闽南语','沪语','维语']
        },
        yAxis: {
          type: 'value',
          axisLabel: {
            align: 'left',
            margin: 60
          }
        },
        grid: {
          width: '80%',
          height: '60%',
          top: '20%',
          left: 95
        },
        series: [{
          name: '上海市男性语种',
          type: 'bar',
          smooth: true,
          // barGap: '50%',
          barCategoryGap: '50%',
          itemStyle: {
            normal: {
              color: 'rgba(110,141,227,1)',
              // width: 3,
              // shadowColor: 'rgba(255,138,0,0.4)',
              // shadowBlur: 8,
              // shadowOffsetY: 20
            }
          },
          /* data: 后面可以为一个立即执行的方法,参看http://www.echartsjs.com/gallery/editor.html?c=dynamic-data
          和 http://www.echartsjs.com/gallery/editor.html?c=radar2&edit=1&reset=1里的写法 */
        //   data: [1000, 1200, 210, 540, 260, 830, 710]
          data: this.statisticArr
        }]
      })
    }
  },
  beforeCreate() {

  },
    created() {
    //   console.log('%cBar_this:','color:orange;font-style:italic;font-weight:700' ,this)

        // 1 -- 语种; this.barChartDatenum -- 时间跨度参数: 1/3/7/30天
        getStatistics( this.barChartDatenum, 1 ).then(res=>{
            // console.warn('~~~~~res.data.Data(语种)__created()初始中~~~~~~:', res.data.Data)
            // var tmp = [];
            for (var i = 0; i < 7; i++) { // 注意: 语种只有7个, ECharts里的横坐标就只有7个, 这里的statisticArr是对应横坐标个数的
                this.statisticArr.push(res.data.msg[i].Count)
                // console.warn('statisticArr[i]:',statisticArr[i])
                // console.warn('async__this:',this)
            }
            // return statisticArr
            // console.error('tmp:',tmp)
            // this.statisticArr = [ ...tmp ];
            // this.statisticArr = tmp;

            this.initChart()
        });
    },
    beforeMount() {
    },
    mounted() {
        console.log('init')
        // this.initChart()
    },
    beforeUpdate() {
    },
    updated() {
    },
    activated() {
    },
    deactivated() {
    },
    beforeDestroy() {
    },
    destroyed() {
    }
}
</script>




